.woocommerce-checkout{    .woocommerce-form,    .woocommerce-form-coupon{        @media ( min-width: #{ $screen-sm } ) {            max-width: 50%;        }        .form-row-first,        .form-row-last{            width: 100%;        }    }    .country_to_state{		-webkit-appearance: none;	}}form.woocommerce-checkout{    @extend .flw;    .woocommerce-billing-fields{        >h3{            margin-bottom: 30px;        }    }    #customer_details{        @extend .flw;        @media ( min-width: #{ $screen-lg } ) {            width: 670px;            padding-right: 50px;        }    }    #order_review_heading{        float: left;        clear: none;        margin-bottom: 58px;    }    #order_review{        @extend .flw;        @media ( min-width: #{ $screen-lg } ) {            @include calc( width, '100% - 720px' );        }        border: 1px solid #eee;        padding: 25px 45px 45px 45px;    }    .shop_table{        border: 0;        margin: 0;        td, th{            border: 0;            padding: 13px 0;            vertical-align: top;            &:last-child{                text-align: right;            }        }        tr{            border-bottom: 1px solid #dddddd;        }        tfoot{            tr th:first-of-type{                font-weight: 700;                text-transform: uppercase;            }        }        .shipping{            th, td{                display: block;                width: 100%;                text-align: left;            }            th{                padding-bottom: 5px;            }            td{                padding: 5px 0;            }            ul{                margin: 0;            }        }        .shipping,        .order-total{            border-bottom: 0;        }    }        /**/    #payment{        margin-top: 10px;    }    .wc_payment_method{        border-bottom: 1px solid #dddddd;        padding: 19px 0;        &:last-of-type{            border-bottom: 0;        }        &.payment_method_paypal{            img{                max-width: 100px;                margin: 0 10px;            }        }        label{            font-size: 18px;        }    }    .payment_box{        padding-left: 28px;        padding-top: 10px;    }    .about_paypal{        font-size: 14px;        position: relative;        top: 2px;    }    #place_order{        border: 0;        color: #fff;        height: 55px;        text-transform: uppercase;        @extend .flw;        @extend .duration;        &:hover{            opacity: 0.8;        }    }    @media ( max-width: #{ $screen-sm - 1 } ) {        #customer_details{            margin-bottom: 30px;        }        #order_review_heading{            margin-bottom: 10px;            float: none;            clear: both;            font-size: 150%;        }        .form-row{            width: 100%;            margin-right: 0;            margin-left: 0;        }        #order_review{            border: 0;            padding: 0;        }    }}@media (max-width: 767px) {    .shop-buttons-is-sticky #place_order {        position: fixed;        bottom: 0;        left: 0;        width: 100%;        display: block;        z-index: 9999;    }}