.scroll-to-top {
	position: fixed;
	z-index: 100;
	right: 20px;
	bottom: -100%;
	width: 40px;
	height: 40px;
	border-radius: 50%;
	color: #ffffff;
	font-size: 16px;
	opacity: 0.5;
	transition: opacity 0.3s ease-in-out, bottom 1s;
	@extend .flex;

	@media (max-width: 767px) {
		display: none;
	}

	&:hover {
		opacity: 1;
		color: #ffffff !important;
	}

	&.is-visible {
		bottom: 20px;
	}
}
