.menu-layout-7{
	.header-container{
		@include flex( flex, space-between, center );
	}
	.m-col{
		@media ( min-width: $screen-md ) {
			max-width: 300px;
			flex-basis: 300px;
			.menu-toggle-btn{
				display: none;
			}
    	}
    	.search-field{
    		background-color: $tr;
    		border-width: 0 0 1px 0;
    		border-radius: 0;
    		padding-left: 0;
    		text-transform: uppercase;
    	}
	}
	.menu-7-content{
		@extend .flex;
	}
	@media ( max-width: $screen-md ) {
		.header-container{
			.search-form{
				display: none;
			}
		}
		.menu-7-content{
			.theme-primary-menu{
				display: none;
			}
		}
	}
}