.widget{
    /*SHOPPING CART*/
    &.widget_shopping_cart{
        .product_list_widget{
            .mini_cart_item{
                padding-right: 0;
                padding-left: 90px;
            }
            img{
                left: 0;
            }
            .quantity{
                @extend .flw;
                height: auto;
            }
        }
        .woocommerce-mini-cart__total{
            margin-top: 15px;
            @extend .flw;
            @include flex( flex, space-between, center )
        }
        .woocommerce-mini-cart__buttons{
            margin-bottom: 0;
            @extend .flw;
            @include flex( flex, space-between, center );
            a{
                width: 50%;
                text-align: center;
                text-transform: uppercase;
                color: #fff;
                font-size: 12px;
                &:hover{
                    opacity: 0.8;
                }
            }
            .wc-forward{
                border-left: 1px solid #fff;
            }
        }
        .variation{
            margin: 5px 0;
            font-size: 13px;
            @extend .flw;
            dt, dd{
                float: left;
                line-height: 18px;
            }
            dt{
                font-weight: normal;
                clear: both;
                margin-right: 5px;
            }
            dd{
                margin: 0;
                p:last-child{
                    margin-bottom: 0;
                }
            }
        }
    }
}

/*PRICE FILTER*/
.price_slider_wrapper{
    height: 4px;
    background: #f3f3f3;
    .price_slider{
        margin-bottom: 30px;
        position: relative;
        @extend .flw;
    }
    .ui-widget-header{
        height: 4px;
        position: absolute;
    }
    .ui-slider-handle{
        cursor: pointer;
        top: -8px;
        position: absolute;
        width: 20px;
        height: 20px;
        margin-left: -10px;
        border-radius: 50%;
        background: #fff;
        @include shadow(1px 1px 3px 0px rgba(0, 0, 0, 0.2));
    }
    .price_slider_amount{
        input, .clear{
            display: none;
        }
        button{
            display: inline-block;
            border: 0;
            color: #fff;
            padding: 6px 17px;
            margin-right: 15px;
            font-size: 12px;
            border-radius: 3px;
            @extend .duration;
            &:hover, &:active{
                opacity: 0.8;
            }
        }
        .price_label{
            display: inline-block;
        }
    }
}

/* PRODUCTS && REVIEWS && TOP RATED */
.widget_products,
.widget_recent_reviews,
.widget_top_rated_products,
.widget_recently_viewed_products{
    .product_list_widget{
        img{
            left: 0;
        }
    }
    li{
        @extend .flw;
        min-height: 110px;
        position: relative;
        padding: 20px 0 20px 90px;
        border-bottom: 1px solid #eaeaec;
    }
    a{
        line-height: 1.3;
        margin-bottom: 5px;
    }
    .star-rating{
        display: block;
        margin-bottom: 10px;
    }
    del{
        margin-right: 5px;
        float: left;
        clear: both;
    }
    ins{
        float: left;
        background: $tr;
    }
}

/* REVIEWS */
.widget_recent_reviews{
    .reviewer{
        float: left;
        clear: both;
    }
}

/* Accordion product category */
.product-categories {
	&.zoa-product-categories {
		.cat-item{
			position: relative;
			
			&:not(.active) {
				.children{
					display: none;
				}
			}
		}

		.accordion-cat-toggle{
			width: 30px;
			height: 30px;
			position: absolute;
			right: 0;
			top: 0;
			cursor: pointer;
			color: #888;
			font-size: 1.1em;
			user-select: none;
			@extend .flex;
			&:before{
				position: relative;
				@extend .duration;
			}
			&.active{
				&:before{
					@include rotate( 90 );
				}
			}
		}
	}
}

/*Featured products*/
.widget_featured_product{
	.widget-title{
		padding-right: 40px;
		position: relative;
	}
}
.widget-featured-carousel-product-arrow{
	float: right;
	position: absolute;
	right: 0;
	top: -2px;
	span{
		cursor: pointer;
		width: 18px;
		height: 25px;
		font-size: 1.1em;
		float: left;
		@include flex( flex, center, center, $i );
	}
}

.widget-featured-carousel-product{
	.slick-slide{
		>div{
			.featured-carousel-product-item{
				width: 100% $i;
			}
		}
	}
	.featured-carousel-product-item{
		margin-bottom: 15px;
		&:after{
			content: '';
			display: block;
			clear: both;
		}
	}
	.fcp-image{
		float: left;
		width: 80px;
		margin-right: 20px;
	}
	.fcp-content{
		float: left;
		@include calc( width, '100% - 100px' );
	}
	.fcp-title{
		font-size: 1em;
		margin-top: 0;
		font-weight: normal;
	}
}

/* Product filter */
.widget-products-filter{
	&.filter-by-color{
		.pf-item{
			margin-right: 15px;
			margin-bottom: 15px;
			&:hover,
			&.active{
				.pf-link{
					&:before{
						border-color: #e8e8e8;
					}
				}
			}
		}
		.pf-link{
			width: 20px;
			height: 20px;
			border-radius: 50%;
			display: block;
			position: relative;
			&:before{
				content: '';
				left: -5px;
				right: -5px;
				bottom: -5px;
				top: -5px;
				position: absolute;
				border-radius: 50%;
				border: 1px solid $tr;
				@extend .duration;
			}
		}
	}
	&.filter-by-image{
		.pf-item {
			width: 40px;
			height: 40px;
			margin-right: 8px;
			margin-bottom: 8px;
			font-size: 0;
			.pf-link{
				display: block;
				border: 1px solid $tr;
				&:hover{
					border-color: #f00;
				}
			}
			&.active{
				.pf-link{
					border-color: #f00;
				}
			}
		}
	}
	&.filter-by-label{
		.pf-item{
			margin-right: 10px;
			margin-bottom: 15px;
			&:hover{
				opacity: 0.8;
			}
			&.active{
				.pf-label{
					background-color: #ccc;
					color: #fff;
				}
				.pf-count{
					background-color: #ddd;
					color: #444;
				}
			}
			span{
				position: relative;
				@extend .duration;
			}
		}
		.pf-link{
			line-height: 2em;
			display: flex;
			justify-content: space-between;
			align-items: center;
		}
		.pf-label{
			padding: 0 1em;
			background-color: #ddd;
			color: #444;
		}
		.pf-count{
			font-size: 0.9em;
			padding: 0 1em;
			background-color: #ccc;
			color: #fff;
		}
	}
	&.filter-by-select{
		.pf-item{
			float: none;
			display: block;
		}
	}

	.product-filter-list{
		margin: 0;
		padding: 0;
		&:after{
			content: '';
			display: block;
			clear: both;
		}
	}

	.pf-item {
		float: left;
	}
}