@media ( max-width: #{ $screen-md - 1 } ) {
    .blog{
        .cover-image{
            max-width: 100%;
            margin-bottom: 20px;
             + .blog-article-sum{
                padding-right: 0;
                max-width: 100%;
             }
        }
        .blog-article{
            margin-bottom: 50px;
        }
        .blog-read-more{
            margin-top: 20px;
        }
    }
    .single{
        .post-nav-item{
            padding: 10px;
            .nav-item-cont{
                .entry-title{
                    font-size: 16px;
                    line-height: 1.4;
                }
            }
        }
    }
    
    /* title for blog widget - elementor */
    .ht-grid-item,
    .blog-article-sum{
        .entry-title{
            font-size: 150%;
        }
    }
}

@media ( max-width: #{ $screen-sm - 1 } ) {
    .single{
        .posts-nav{
            flex-direction: column;
        }
        .prev-nav{
            .nav-item-cont{
                margin-left: 15px;
            }
            img{
                margin-right: 0;
            }
        }
        .post-nav-item{
            width: 100%;
            img{
                width: 70px;
                height: 70px;
            }
            .nav-item-cont{
                @include calc( max-width, '100% - 85px' );
            }
            &.next-nav{
                justify-content: flex-start;
                .nav-item-cont{
                    order: 2;
                    margin-right: 0;
                    margin-left: 15px;
                    text-align: left;
                }
            }
        }
        #commentform #author,
        #commentform #email{
            width: 100%;
            margin-right: 0;
        }
    }
}

@media ( max-width: 600px ) {
    .single-post{
        .comment-item{
            margin-bottom: 30px;
            padding-bottom: 20px;
        }
        .comment-avatar{
            width: 60px;
            margin-right: 0;
            margin-bottom: 15px;
        }
        .comment-content{
            width: 100%;
        }
    }
}

@media ( max-width: #{ $screen-ios } ) {
    #commentform{
        #submit{
            width: 100%;
        }
    }
}