.zoa-category-box-overlay {
  position: relative;

  .zoa-category-box-content {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
  }

  .zoa-category-box-inner {
    position: absolute;
  }
}
.zoa-category-box-wrapper{
	position: relative;
}
.zoa-category-box-link{
	position: absolute;
	left: 0;
	top: 0;
	right: 0;
	bottom: 0;
	z-index: 5;
}
.zoa-category-box-top-left {
  .zoa-category-box-content {
    justify-content: flex-start;
    align-items: flex-start;
  }
}

.zoa-category-box-top-center {
  .zoa-category-box-content {
    justify-content: flex-start;
    align-items: center;
  }
}

.zoa-category-box-top-right {
  .zoa-category-box-content {
    justify-content: flex-start;
    align-items: flex-end;
  }
}

.zoa-category-box-center-left {
  .zoa-category-box-content {
    justify-content: center;
    align-items: flex-start;
  }
}

.zoa-category-box-center-center {
  .zoa-category-box-content {
    justify-content: center;
    align-items: center;
  }
}

.zoa-category-box-center-right {
  .zoa-category-box-content {
    justify-content: center;
    align-items: flex-end;
  }
}

.zoa-category-box-bottom-left {
  .zoa-category-box-content {
    justify-content: flex-end;
    align-items: flex-start;
  }
}

.zoa-category-box-bottom-center {
  .zoa-category-box-content {
    justify-content: flex-end;
    align-items: center;
  }
}

.zoa-category-box-bottom-right {
  .zoa-category-box-content {
    justify-content: flex-end;
    align-items: flex-end;
  }
}

.zoa-category-box-image {
  overflow: hidden;

  img {
    display: block;
    margin: 0 auto;
    transition: transform 0.3s ease-out;
  }
}

.zoa-category-box-title {
  margin-top: 0;
  margin-bottom: 0;
}

.zoa-category-box-wrapper:hover {
  .zoa-category-box-image img {
    transform: scale(1.1);
  }
}
