@media ( max-width: 1800px ) {
    #shop-quick-view{
        width: 992px;
        height: 500px;
        @include calc( top, '( 100% - 500px ) / 2' );
        @include calc( left, '( 100% - 992px ) / 2' );
    }

    #quick-view-gallery{
        height: 500px;
    }
    
    .quick-view-open{
        .quick-view-content{
            .summary.entry-summary{
                width: 50%;
                padding-left: 30px;
                height: 500px;
            }
        }
    }
}

@media ( max-width: 1600px ) {
    .quick-view-open{
        &.cart-sidebar-open{
            &.cart-updated{
                #shop-quick-view{
                    opacity: 0;
                    visibility: hidden;
                    pointer-events: none;
                    @include translatey(50px)
                }
            }
        }
    }
}

@media ( max-width: #{ $screen-md - 1 } ) {
    #shop-quick-view{
        width: 100%;
        height: 400px;
        max-height: 70%;
        left: 15px;
        @include calc( width, '100% - 30px' );
        @include calc( top, '( 100% - 400px ) / 2' );
    }

    #quick-view-gallery{
        height: 400px;
    }
    
    .quick-view-open{
        .quick-view-content{
            .quick-view-images{
                .tns-controls{
                    display: none;
                }
                .tns-nav{
                    bottom: 30px;
                    opacity: 1;
                }
            }
            .summary.entry-summary{
                width: 50%;
                padding-left: 30px;
                height: 400px;
            }
        }
    }
}

@media ( max-width: #{ $screen-sm - 1 } ) {
    #shop-quick-view{
        left: 15px;
        right: 15px;
        overflow-y: auto;
        overflow-x: hidden;
    }
    
    #quick-view-gallery{
        height: auto;
    }

    .quick-view-content{
        .quick-view-images{
            width: 100%;
        }
    }

    .quick-view-open{
        overflow: auto;
        .quick-view-close-btn{
        	display: none;
        }
        #shop-overlay{
        	&:before{
        		content: "\f404";
        		font-family: $ion;
        		position: absolute;
        		@include calc( top, '( 100% - 480px ) / 2' );
        		background-color: #000;
        		color: #fff;
        		width: 40px;
        		height: 40px;
        		display: flex;
        		justify-content: center;
        		align-items: center;
        		right: 15px;
        		color: #fff;
        		font-size: 30px;
        	}
        }
        .quick-view-content{
            .summary.entry-summary{
                height: auto;
                width: 100%;
                padding: 0 15px;
            }
        }
    }
}