/* EDIT LOCATION
------------------------------------------------->*/
.customize-partial-edit-shortcut{
    width: 30px;
    height: 30px;
    top: 50px;
    left: 20px;
    z-index: 21;
    .menu-layout &{
        display: none;
    }
    #theme-page-header &{
        top: 15px;
    }
    .customize-partial-edit-shortcut-button{
        left: 0;
        top: 0;
    }
}

/* BLANK MENU
------------------------------------------------->*/
.add-menu{
    line-height: 85px;
    display: inline-block;
    + .add-menu{
        margin-left: 20px;
    }
}

/*! ELEMENTOR EDIT MODE
------------------------------------------------->*/
.elementor-editor-active{
    .elementor-element-edit-mode{
        min-height: 20px;
    }
}

/*! G_CAPCHA
******************************************************************************************************/
.g-recaptcha{
	position: static $i;
	clear: both;
	margin-bottom: 10px;

	.single &{
		margin-bottom: 30px;
	}
}

/*! FOR PEN-MENU WHEN TEXT SELECTED
------------------------------------------------->*/
.pen-menu{
	z-index: 100 $i;
}