.woocommerce-wishlist{
    @media ( max-width: #{ $screen-md - 1 } ) {
        .wishlist-title{
            h2{
                font-size: 150%;
            }
        }
    }

    .wishlist_table{
        border-left: 0;
        border-right: 0;
        border-top: 0;
        thead{
            th{
                border: 0;
                background: #f3f3f3;
            }
        }
        th, td{
            border-left: 0;
            border-right: 0;
            padding: 20px 0;
        }
        .product-remove{
            @media ( min-width: #{ $screen-sm } ) {
                width: 50px;
                text-align: center;
            }
            font-size: 16px;
        }
        .product-thumbnail{
            img{
                max-width: 80px;
                max-height: 80px;
            }
        }
        @media ( max-width: #{ $screen-sm - 1 } ) {
            .product-remove{
                padding: 10px;
                background: #f3f3f3 $i;
                a{
                    color: #f00
                }
            }
            .pro-swatch-list{
                float: none;
            }
            thead{
                display: none;
            }
            td{
                display: block;
                padding: 10px 0;
            }
            tr{
                &:last-of-type{
                    td{
                        &:last-of-type{
                            border-bottom: 0
                        }
                    }
                }
            }
        }
    }

    .yith-wcwl-share{
        @extend .theme-social-icon;
        .yith-wcwl-share-title{
            margin-bottom: 0;
            padding: 0 10px;
        }
        ul{
            padding-left: 0;
            margin: 0;
        }
        li{
            a{
                width: 30px;
                height: 30px;
                @extend .flex;
                &:before{
                    font-size: 18px;
                }
            }
        }
    }
}