.topbar {
	display: none;
	padding-top: 10px;
	padding-bottom: 10px;
	background-color: #f7f7f7;

	@media (min-width: 992px) {
		display: block;
	}

	.container {
		@media (min-width: 1380px) {
			width: 100%;
			padding-right: 105px;
			padding-left: 105px;
		}
	}
}

.topbar__container,
.topbar__left,
.topbar__right {
	display: flex;
}

.topbar__container {
	justify-content: space-between;
	align-items: center;
}

.topbar__social {
	margin-left: 30px;
}
