.group-blog-sidebar-left{
	#theme-menu-pusher{
		>.container{
			padding: 0;
			>.row{
				@media ( max-width: $screen-md - 1 ) {
					width: 100%;
					margin: 0;
					display: flex;
					flex-direction: column;
					.col-md-3{
						order: 2;
						margin-top: 40px;
					}
				}
			}
		}
	}
}
.group-blog {
    .blog-title {
        font-weight: normal;
    }
}
.blog {
    .cover-image {
        float: right;
        @include calc('max-width', '100% - 500px');
        + .blog-article-sum {
            max-width: 500px;
            padding-right: 100px;
        }
    }
    .blog-article-sum {
        @extend .flw;
    }
}


.has-menu-open {
	overflow: hidden;
}