#theme-footer {    @extend .flw;    margin-top: 50px;    position: relative;}.footer-widget {    float: left;}.footer-bot {    @extend .flw;    border-top: 1px solid #eeeeee;    padding: 30px 0;}.footer-logo {    min-height: 1px;    @extend .flw;    .customize-partial-edit-shortcut {        display: none;    }    @media (min-width: 992px) {        width: 20%;    }    @media (max-width: 991px) {        text-align: center;        margin-bottom: 15px;        &:empty {            display: none;        }    }    img {        max-width: 84px;    }}.footer-copyright {    text-align: center;    min-height: 1px;    @extend .flw;    @media (min-width: 992px) {        width: 60%;    }    @media (max-width: 991px) {        margin-bottom: 15px;        &:empty {            display: none;        }    }}.footer-bot-right {    text-align: right;    min-height: 1px;    @extend .flw;    @media (min-width: 992px) {        width: 20%;    }    @media (max-width: 991px) {        text-align: center;    }    p {        &:last-child {            margin-bottom: 0;        }    }    .fa {        font-size: 0;        display: inline-block;        margin-left: 18px;        + br {            display: none;        }        &:before {            font-size: 17px;        }    }}