.yith-wcwl-add-to-wishlist{	.single-product &{		>a{			font-size: 14px;			&:first-child:last-child{				&:before{					margin-right: 10px;				}			}		}		}	>a{		&:first-child:last-child{			&:before{				content: "\e912";				font-family: 'zoa';				font-size: 14px;			}		}	}}.yith-wcwl-add-to-wishlist {	.loop-action & {		height: 35px;		display: inline-block;		margin-right: 10px;		@media (min-width: 1200px) {			height: 45px;		}	}	@extend .duration;	div:empty {		display: none;	}	.ajax-loading {		display: none $i;	}	.yith-wcwl-add-button,	.yith-wcwl-wishlistaddedbrowse,	.yith-wcwl-wishlistexistsbrowse {		a {			&:before {				content: "\f442";				font-family: $ion;				font-size: 20px;				line-height: 1;			}		}	}	.yith-wcwl-wishlistaddedbrowse {		a {			&:before {				content: "\f3fd";				font-family: $ion;				font-size: 30px;			}		}	}	.yith-wcwl-wishlistexistsbrowse {		a {			&:before {				content: "\f443";			}		}	}}.loop-action {	.clear {		display: none;	}}/* notice after added to wishlist */#yith-wcwl-popup-message {	display: none $i;}