.wd-pro-flash-sale{    @extend .flw;    text-align: center;    .price{        font-size: 16px;    }}.flash-sale-cd{    @extend .flex;    margin: 50px 0 30px 0;    .cd-item{        @extend .flex;        flex-direction: column;        position: relative;        padding-right: 40px;        margin-right: 30px;        &:after{            content: ':';            font-size: 24px;            top: 2px;            position: absolute;            right: 0;        }        &:last-of-type{            margin-right: 0;            padding-right: 0;            &:after{                content: none;            }        }    }    .cd-time{        @extend .flex;        line-height: 36px;        font-size: 36px;    }}.flash-sale-atc{    a{        width: 180px;        height: 50px;        padding: 0 15px;        color: #fff $i;        margin-top: 40px;        @extend .inline-flex;        &:hover{            opacity: 0.8;        }        &.loading{            &:before{                content: none $i            }        }        &.added{            display: none;        }        &.added_to_cart{            font-size: 14px;            &:before{                content: none $i            }        }        &.disable{            pointer-events: none;            opacity: 0.3;        }    }}