@media (max-width: 1500px) {
	.cart {
		.single_add_to_cart_button {
			width: auto;
			padding: 0 30px;
		}
	}
}
@media (max-width: #{ $screen-lg - 1 }) {
	.single-gallery-slider, .single-gallery-image {
		.single-product-gallery {
			width: 50%;
			+ .summary {
				padding-left: 30px;
			}
		}
		.summary {
			width: 50%;
		}
	}
	.single-gallery-image {
		.single-product-gallery {
			margin-right: 30px;
			+ .summary {
				padding-left: 0;
			}
		}
		.summary {
			@include calc(width, '50% - 30px');
		}
	}
}
@media (max-width: #{ $screen-md - 1 }) {
	.hover-product-image,
	.pro-carousel-thumb .tns-controls {
		display: none;
	}
	.product_list_widget {
		.mini_cart_item {
			.remove_from_cart_button {
				opacity: 1;
				@include transforms(translateY(-50%) translateX(0) rotate(0deg));
			}
		}
	}
	.single-gallery-image {
		.single-product-gallery {
			width: 100%;
			margin-right: 0;
		}
		.summary.entry-summary {
			width: 100%;
			padding-left: 0;
		}
	}
	.single-gallery-slider {
		.single-product-gallery {
			width: 100%;
			margin-right: 0;
		}
		.summary.entry-summary {
			width: 100%;
			padding-left: 0;
		}
	}
	.shop-container {
		.shop-content {
			float: left;
			width: 100%;
			+ .shop-sidebar {
				margin-top: 50px;
			}
		}
		.shop-sidebar {
			float: left;
			width: 100%;
		}
	}
	.single-product {
		.woocommerce-Reviews #commentform .comment-form-author, .woocommerce-Reviews #commentform .comment-form-email {
			width: 100%;
			margin-left: 0;
			input {
				margin-bottom: 10px $i;
			}
		}
		.ph-layout-2 {
			height: auto;
			margin-bottom: 0;
			.container {
				flex-direction: column;
				align-items: flex-start;
			}
		}
	}
}

@media ( max-width: $screen-md - 1 ) {
	.single-gallery-image{
		.shop-content{
			.pro-img-item{
				margin-bottom: 0;
			}
		}

		.tns-inner.tns-ah{
			overflow: hidden;
		}

		.tns-nav{
			button{
				border: 1px solid #ccc;
			}
		}
	}
}

@media (max-width: 720px) {
	.single-gallery-slider {
		&.single-gallery-vertical, &.single-gallery-horizontal {
			.pro-carousel-image {
				width: 100%;
			}
		}
	}
	.summary {
		.product_title {
			margin-top: 25px;
			font-size: 200%;
		}
	}
	.woocommerce-tabs {
		.tabs {
			margin: 30px 0;
			text-align: left;

			li {
				padding: 2px 0;

				+li{
					margin-right: 10px;
				}

				&.active{
					a{
						border-bottom-width: 2px;
					}
				}
			}

			a {
				font-size: 14px;
				font-weight: normal;
				padding: 5px 10px;
				border: 1px solid #ccc;
			}
		}
	}

	.single-gallery-slider.single-gallery-vertical{
		.shop-content {
			> .this-product-has-gallery-image {
				> .onsale{
					left: 20px;
				}
			}
		}
	}
}
@media (max-width: 600px) {
	.shop-top-bar {
		display: block;
		.woocommerce-result-count {
			margin-bottom: 5px;
			font-size: 13px;
		}
		.woocommerce-ordering {
			select {
				width: 100%;
				padding: 0 10px;
				border-radius: 4px;
			}
		}
	}
	.single-product {
		.woocommerce-tabs {
			.woocommerce-Tabs-panel {
				margin-bottom: 50px;
			}
		}
		.woocommerce-Reviews {
			.comment_container {
				padding-bottom: 20px;
				margin-bottom: 30px;
			}
			.comment_container img + .comment-text {
				width: 100%;
				margin-left: 0;
				margin-top: 15px;
			}
		}
		.upsells.products, .related.products {
			padding-top: 0;
			> h2 {
				font-size: 200%;
				margin: 30px 0;
			}
		}

		.tawcvs-swatches{

			.swatch {
				margin-bottom: 20px;
			} 
		}
	}
}

// 375 >= From iPhone 2G to iPhone 6s, not plus :)
@media (max-width: 600px ) {
	.single-product {
		.cart {
			.quantity {
				width: 100%;
			}
		}
		.single_add_to_cart_button {
			margin-top: 10px;
			margin-left: 0;
			width: 100%;
			padding-left: 15px;
			padding-top: 15px;
		}
	}
}

@media (max-width: 767px) {
	.shop-buttons-is-sticky .single_add_to_cart_button {
		position: fixed;
		bottom: 0;
		left: 0;
		width: 100%;
		margin-left: 0;
		z-index: 9999;
	}
}

.shop-container.has-flexible-sidebar {
	@media (max-width: #{ $screen-md - 1 }) {
		.shop-sidebar {
			float: none;
			width: 280px;
			margin-top: 0;
			position: fixed;
			top: 0;
			left: 0;
			height: 100%;
			padding: 25px;
			background-color: #ffffff;
			transition: transform 0.5s ease-in-out;
			transform: translateX(-280px);
			z-index: 1000;
			overflow: auto;
		}
		.shop-sidebar.is-visible {
			transform: translateX(0);
		}
	}
	@media (min-width: $screen-md) {
		.sidebar-toggle, .sidebar-overlay {
			display: none;
		}
	}
	.logged-in.admin-bar & .shop-sidebar {
		padding-top: 71px;
		@media (min-width: 783px) {
			padding-top: 57px;
		}
	}
}
.sidebar-overlay {
	position: fixed;
	top: 0;
	right: 0;
	bottom: 0;
	left: 0;
	background-color: rgba(0, 0, 0, 0.3);
	opacity: 0;
	visibility: hidden;
	z-index: -1;
	transition: opacity 0.5s ease-in-out;
}
.sidebar-overlay.is-visible {
	opacity: 1;
	visibility: visible;
	z-index: 999;
}
.sidebar-toggle {
	position: fixed;
	left: 0;
	top: 20%;
	z-index: 999;
	display: inline-block;
	width: 40px;
	height: 40px;
	border: 1px solid #aaaaaa;
	background-color: #ffffff;
	font-size: 20px;
	text-align: center;
	line-height: 40px;
	transition: transform 0.5s ease-in-out, color 0.5s, box-shadow 0.5s;
	transform: translateX(0);
	&:hover {
		box-shadow: 0 0 11px rgba(33, 33, 33, 0.2);
	}
	&.is-active {
		transform: translateX(280px);
	}
}
body.hide-scrollbar {
	overflow: hidden;
	@media (min-width: $screen-md) {
		overflow: auto;
	}
}