/* icon menu */
.theme-icon-wp-menu{
    margin-right: 3px;
}
/*mega menu*/
@media (min-width: 992px){
    .theme-primary-menu{
        &:not( .theme-sidebar-menu ) {
            .menu-item-has-mega-menu{
                &:hover{
                    .mega-menu-row{
                        opacity: 1;
                        visibility: visible;
                        top: 100%;
                        @include scaleY;
                    }
                }
            }
            /*mega menu row*/
            .mega-menu-row{
                position: absolute;
                left: 0;
                top: 110%;
                visibility: hidden;
                opacity: 0;
                z-index: 10;
                text-align: left;
                margin-left: 0;
                padding: 20px 20px 0 20px;
                background-color: #fff;
                width: 800px;
                @include scaleY(0);
                @include transitions();
                *{
                    font-family: $inh;
                    font-size: $inh;
                    font-weight: $inh;
                    color: $inh;
                }
                &.mega-menu-col-2{
                    width: 400px;
                    left: -100px;
                    &.mega-menu-in-right{
                        left: $ini;
                        right: -100px;
                    }
                    >li{
                        width: 50%;
                    }
                }
                &.mega-menu-col-3{
                    width: 600px;
                    left: -150px;
                    &.mega-menu-in-right{
                        left: $ini;
                        right: -150px;
                    }
                    >li{
                        width: percentag( 1 / 3 );
                    }
                }
                &.mega-menu-col-4{
                    left: -200px;
                    &.mega-menu-in-right{
                        left: $ini;
                        right: -200px;
                    }
                }
                > li{
                    float: left;
                    margin-bottom: 20px;
                    width: 25%;
                    > a{
                        padding: 0 10px;
                        margin-bottom: 15px;
                        display: block;
                        font-weight: 600;
                        @include calc(font-size, '100% + 2px');
                    }
                }
                li{
                    overflow: hidden;
                    span:not(.theme-icon-wp-menu){
                        color:#b6b6b6;
                        font-size: 12px;
                        line-height: 24px;
                        margin-bottom: 10px;
                    }
                    div{
                        line-height: 24px;
                        color: #b6b6b6;
                        font-size: 12px;
                        padding: 5px 10px;
                        p{
                            margin-top: 10px;
                            color: #b6b6b6;
                        }
                    }
                    ul{
                        position: static;
                        float: left;
                        border:0;
                        width: 100%;
                        li{
                            width: 100%;
                            padding: 0 0 0 1px;
                        }
                    }
                }
                .sub-menu{
                    margin-left: 0;
                    left: 0;
                    position: static;
                    background: $tr;
                    a{
                        display: block;
                        padding: 5px 10px;
                        white-space: nowrap;
                    }
                }
            }
            .mega-menu-col{
                li{
                    display: inline-block;
                }
                .sub-menu{
                    width: 100%;
                }
            }
        }
    }
}