.select2-container{    .select2-selection--single{        height: 55px;        margin: 0;        border-radius: 0;        border-color: #f3f3f3;        background: #f9f9f9;    }}.select2-container--default{    .select2-selection--single{        .select2-selection__rendered{            height: 55px;            line-height: 55px;            padding: 0 20px;        }        .select2-selection__arrow{            right: 0;            top: 0;            width: 20px;            height: 55px;            @extend .flex;        }    }    .select2-search--dropdown{        .select2-search__field{            border-color: #ddd;        }    }}.select2-dropdown{    border-color: #f3f3f3;}