/* Add to cart */
.elementor-add-to-cart{
	.woocommerce-variation-add-to-cart {
		display: block $i;
	}
	.variations{
		border: 0;
		td{
			border: 0;
			padding: 0 10px;
			&.value{
				max-width: 50%;
				&:before{
					content: none $i;
				}
			}
		}
		tr{
			&:last-of-type{
				.label{
					margin-bottom: 0;
				}
			}
		}

		@media ( max-width: 600px ) {
			tr {
				display: block $i;
				&:last-of-type{
					margin-bottom: 0 $i;
					.label {
						margin-bottom: 13px $i;
					}
				}
			}
			.label,
			.value {
				width: 100% $i;
				display: block $i;
				max-width: 100% $i;
			}
			.reset_variations {
				margin-left: 0;
			}
			.single_add_to_cart_button {
				margin-left: 0 $i;
			}
		}
	}
	.reset_variations{
		margin-top: 7px;
		margin-left: 20px;
		float: none;
	}
}

/* Product tabs */
.elementor-widget-woocommerce-product-data-tabs {
	.woocommerce-tabs {
		.panel{
			border-top: 0 !important;
		}
	}
}