.footer-col-1{    .footer-widget{        width: 100%;    }}@media (min-width: 992px){    .footer-col-3{        .footer-widget{            @include calc(width, '100% / 3');            padding-left: 15px;            padding-right: 15px;            &:nth-of-type(3n+4){                clear: both;            }        }    }    .footer-col-4{        .footer-widget{            @include col(25%, 15px);            &:nth-of-type(4n+5){                clear: both;            }        }    }    .footer-col-5{        .footer-widget{            @include col(20%, 15px);            &:nth-of-type(5n+6){                clear: both;            }        }    }    .footer-col-6{        .footer-widget{            @include calc(width, '100% / 6');            padding-left: 15px;            padding-right: 15px;            &:nth-of-type(6n+7){                clear: both;            }        }    }}@media (min-width: 600px){    @media (max-width: 991px){        .footer-col-3,        .footer-col-4,        .footer-col-5,        .footer-col-6{            .footer-widget{                @include col(50%, 15px);                &:nth-of-type(2n+3){                    clear: both;                }            }        }    }    .footer-col-2{        .footer-widget{            @include col(50%, 15px);            &:nth-of-type(2n+3){                clear: both;            }        }    }}@media (max-width: 599px){    .footer-col-2,    .footer-col-3,    .footer-col-4,    .footer-col-5,    .footer-col-6{        .footer-widget{            @include col(100%, 15px);            clear: both;        }    }}