.menu-open{    overflow: hidden;}#theme-menu-pusher {    @extend .flw;    background: #fff;    position: relative;    z-index: 99;    @include transition(transform 0.5s);    .menu-open & {        @include translate3d( 300px, 0, 0 );        @media ( min-width: #{ $screen-sm } ) {            @include translate3d( 345px, 0, 0 );        }        #menu-overlay {            visibility: visible;            pointer-events: auto;            z-index: 99;            opacity: 1;        }    }}.menu-layout {    border-bottom: 1px solid #eeeeee;    #menu-box {        @include flex(flex, space-between, center);    }	.m-col{		flex-basis: 90px;    	@media ( min-width: $screen-md ) {    		flex-basis: 170px;    	}    }    .primary-logo{    	width: 100%;    }    .m2-col {        @include flex(flex, flex-end, center);    }    .menu-toggle-btn {        span {            @include delay(0.5s);            @include duration(0.5s);            &:before,            &:after {                @include delay(0.5s);                @include duration(0.5s);            }        }        .menu-open & {            span {                @include rotate(180);                &:before {                    width: 50%;                    @include transform(rotate(45deg) translate(14px, -11px));                }                &:after {                    width: 50%;                    @include transform(rotate(-45deg) translate(14px, 11px));                }            }        }    }}#sidebar-menu-content {    position: fixed;    top: 0;    left: 0;    bottom: 0;    z-index: 1;    visibility: hidden;    background: #fff;    overflow: auto;    width: 300px;    padding: 30px;    @include translate3d(-50%, 0, 0);    @include transition(all 0.5s);    @media ( min-width: #{ $screen-sm } ) {        padding: 40px 60px;        width: 345px;    }    .menu-open & {        visibility: visible;        z-index: 100;        @include translate3d(0, 0, 0);        @include transition(transform 0.5s);    }    .logged-in.admin-bar & {        top: 46px;        @media (min-width: 783px) {            top: 32px;        }    }    /*LOGO*/    .theme-logo {        max-width: 100%;        margin-bottom: 25px;    }    /*MAIN MENU*/    .theme-primary-menu {        padding: 0;        margin: 0;        .sub-menu {            overflow: hidden;            display: none;        }        > li {            &.current-menu-ancestor,            &.current-menu-parent,            &.current_page_parent,            &.current_page_ancestor {                position: relative;                > a {                    &:before {                        content: '';                        width: 45px;                    }                }            }        }        li {            list-style: none;            &.menu-item-has-children {                position: relative;                &:after {                    content: '\e902';                    font-family: zoa;                    position: absolute;                    right: 0;                    font-size: 5px;                    line-height: 1;                    top: 25px;                }            }            ul {                margin-left: 15px;            }        }        a {            width: 100%;            display: block;            color: #222222;            text-transform: uppercase;            font-weight: 700;            font-size: 13px;            line-height: 50px;            position: relative;            &:hover {                &:before {                    width: 45px;                }            }            &:before {                content: '';                position: absolute;                left: 0;                bottom: 8px;                height: 2px;                width: 0;                @extend .duration;            }        }    }    /*EMPTY MENU*/    .add-menu {        display: block;        line-height: 50px;    }}#menu-overlay {    opacity: 0;    visibility: hidden;    pointer-events: none;    position: fixed;    left: 0;    right: 0;    bottom: 0;    top: 0;    background: rgba(0, 0, 0, 0.3);    @extend .duration;}.header-action {    display: flex;    align-items: center;}