.menu-layout--classic {
	.header-container {
		display: flex;
		align-items: center;
		padding: 15px 0;
	}

	.header-logo {
		margin-right: auto;
		margin-left: auto;
	}

	.header-menu {
		display: none;
	}

	.header-container__inner {
		margin-right: auto;
		margin-left: auto;
	}



	@media (min-width: 992px) {
		&.menu-layout-2,
		&.menu-layout-3{
			.m1-col{
				display: none;
			}
		}
		.header-container {
			justify-content: space-between;
			padding: 0;
		}

		.header-logo {
			margin-left: 0;
			padding: 0;
		}

		.header-container__inner {
			display: flex;
			align-items: center;
			margin-left: 0;

			.header-logo {
				margin-right: 50px;
			}
		}

		.header-menu {
			display: block;
		}

		.header-action {
			display: flex;
			margin-left: 30px;
		}

		.menu-toggle-btn {
			display: none;
		}
	}

	@media (min-width: 1200px) {
		.header-action {
			margin-left: 100px;
		}
	}
}

.menu-layout--transparent {
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
}

.menu-layout--offset-top {
	position: absolute;
	top: 47px;
	left: 0;
	width: 100%;
}

.menu-layout--transparent.menu-layout {
	border-bottom: 0;
}
