#theme-page-header {    @extend .flw;    position: relative;}.page-header {    @extend .flw;    position: relative;    padding: 15px 0;    &.ph-layout-1 {        text-align: center;        .container {            @extend .flex;            flex-direction: column;            height: 100%;        }    }    &.ph-layout-2 {        .container {            @include flex(flex, space-between, center);            height: 100%;        }        .shop-single-nav {            position: relative;            z-index: 1;            a {                font-size: 7px;                &:before {                    font-family: 'zoa';                    content: '\e90e';                }                + a {                    margin-left: 25px;                }            }        }        .ssv-prev {            display: inline-flex;            @include rotate(-180);        }    }}#page-header-parallax {    background-position: 0 0;}.page-title {    line-height: 1.2;    margin: 0;    font-size: 36px;}.crumbs {    display: block;    > span {        font-size: 13px;        &:after {            content: '\f3d3';            font-family: $ion;            margin-left: 8px;            margin-right: 4px;        }    }    .last-item {        span {            font-weight: 600;        }        &:after {            content: none;        }    }}.page-header {    @media (min-width: 1380px) {        .container {            width: 100%;            padding-left: 105px;            padding-right: 105px;        }    }    .customize-partial-edit-shortcut {        display: none;    }}