.woocommerce-form-login,.woocommerce-form-register,.woocommerce-form-coupon{    .form-row{        input{            &:not([type="submit"]){                height: 40px;            }        }    }    button[type="submit"]{        color: #fff;        border: 0;        height: 40px;        padding: 0 20px;        margin-right: 10px;    }}.woocommerce-account{    h2{        font-size: 20px;    }    .u-column1{        @extend .flw;        margin-bottom: 30px;        @media ( min-width: #{ $screen-sm } ) {            @include calc( max-width, '( 100% - 30px ) / 2' );            margin-right: 30px;            margin-bottom: 0;        }        .woocommerce-form-login{            width: 100%;            max-width: 100%;        }    }    .u-column2{        @extend .flw;        @media ( min-width: #{ $screen-sm } ) {            @include calc( max-width, '( 100% - 30px ) / 2' );        }    }}.woocommerce-MyAccount-navigation{    @extend .flw;    margin-bottom: 30px;    @media ( min-width: #{ $screen-md } ) {        margin-right: 130px;        margin-bottom: 0;        max-width: 270px;    }    ul{        list-style: none;        margin: 0;        padding-left: 0;    }    .woocommerce-MyAccount-navigation-link{        margin-bottom: 20px;        &.is-active{            a{                font-weight: 700;            }        }        a{            font-size: 15px;            @include flex( flex, flex-start, center );            &:before{                font-size: 20px;                font-family: 'zoa';                margin-right: 15px;                line-height: 0;            }        }    }    .woocommerce-MyAccount-navigation-link--dashboard{        a:before{            font-family: $ion;            font-weight: 700;            content: "\f447";        }    }    .woocommerce-MyAccount-navigation-link--orders{        a:before{            content: "\e90a";        }    }    .woocommerce-MyAccount-navigation-link--downloads{        a:before{            font-family: $ion;            font-weight: 700;            content: "\f407";        }    }    .woocommerce-MyAccount-navigation-link--edit-address{        a:before{            content: "\e901";        }    }    .woocommerce-MyAccount-navigation-link--edit-account{        a:before{            content: "\e913";        }    }    .woocommerce-MyAccount-navigation-link--customer-logout{        a:before{            content: "\e909";        }    }}.woocommerce-MyAccount-content{    @extend .flw;    @media ( min-width: #{ $screen-md } ) {        @include calc( width, '100% - 400px' );    }    .woocommerce-EditAccountForm{        button[type="submit"]{            @extend .flw;            height: 55px;            color: #fff;            border: 0;            margin-top: 40px;            @extend .duration;            &:hover{                opacity: 0.8;            }        }    }    fieldset{        border: 1px solid #ddd;        margin: 0;        padding: 5px 15px;    }}.woocommerce-address-fields{    .button{        height: 50px;        color: #fff;        padding: 0 25px;        border: 0;        &:hover{            @extend .duration;            opacity: 0.7;        }    }}.lost_reset_password{	button{		&[type="submit"]{			color: #fff;			padding: 10px 20px;		}	}}