.zoa-search-form-container {
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: -1;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: rgba(0, 0, 0, 0.89);
    opacity: 0;
    visibility: hidden;

    &.is-open {
        opacity: 1;
        visibility: visible;
        z-index: 9999;

        .zoa-search-form__inner {
            animation: open-modal 1s linear forwards;
        }
    }
}

.zoa-search-form {
    position: relative;
    margin-bottom: 0;
}

.zoa-search-form__inner {
    max-width: 768px;
    margin: 0 auto;
}


.zoa-search-form__loader {
    position: absolute;
    right: 0;
    top: 10px;
    color: #fff;
    opacity: 0;
    visibility: hidden;

    &.is-active {
        opacity: 1;
        visibility: visible;
    }
}

.zoa-search-form__field {
    display: block;
    width: 100%;
    border: 0;
    border-bottom: 1px solid #343434;
    padding: 15px 0;
    background-color: transparent;
    color: #fff;
    font-size: 16px;
    letter-spacing: 3px;
}

.zoa-search-form__close {
    position: absolute;
    top: -40px;
    right: 10px;
    color: #999999;
    font-size: 30px;
    z-index: 102;
    transition: color 0.3s, transform 0.3s;
    cursor: pointer;

    &:hover {
        color: #ffffff;
        transform: rotate(90deg);
    }
}

.zoa-search-form__desc {
    display: block;
    margin-top: 5px;
    color: #fff;
    font-size: 10px;
    text-transform: uppercase;
    text-align: right;
}

.zoa-search-form__suggestions {
    position: relative;

    .autocomplete-suggestions {
        overflow: auto;
        padding: 10px;
        background-color: #fff;
    }
}

.autocomplete-suggestion {
    display: flex;
    padding-bottom: 10px;
    border-bottom: 1px solid #dddddd;
    margin-bottom: 10px;

    &:last-of-type {
        padding-bottom: 0;
        border-bottom: 0;
        margin-bottom: 0;
    }
}

.autocomplete-suggestion__thumbnail {
    flex-shrink: 0;
    max-width: 60px;
    margin-right: 10px;
}

.autocomplete-suggestion__title {
    margin: 0;
    font-size: 18px;
}

