.woocommerce-cart{    .return-to-shop{        .wc-backward{            font-size: 16px;            font-weight: 700;        }    }    .woocommerce-cart-form{        @extend .flw;        table{            border: 0;        }        thead{            tr{                border-bottom: 0;            }        }        tr{            border-bottom: 1px solid #eaeaea;            position: relative;        }        td, th{            border: 0;            padding: 20px 0;        }        th{            background: #f3f3f3;        }        .product-remove{            width: 50px;            .remove{                width: 50px;                height: 50px;                float: left;                font-size: 20px;                @extend .flex;            }        }        .product-thumbnail{            width: 100px;            img{                max-width: 80px;            }        }        .quantity{            height: 40px;        }        .product-name{            @media ( min-width: #{ $screen-lg } ) {                width: 500px;            }        }        .product-subtotal{            text-align: right;            padding-right: 15px;        }        .actions{            padding-left: 15px;            padding-right: 15px;            .coupon{                float: left;                label{                    display: none;                }                input{                    height: 40px;                    padding: 0 15px;                    margin-right: 10px;                    &:focus{                        border-color: #ddd;                    }                }            }            button[type="submit"]{                float: right;                height: 40px;                padding: 0 15px;                color: #fff;                border: 0;                @extend .duration;                &:disabled{                    opacity: 0.3;                    pointer-events: none;                }                &:hover{                    opacity: 0.8;                }            }            .cart-empty-btn{                float: right;                line-height: 40px;                margin-right: 20px;            }        }    }    .cart-collaterals{        @extend .flw;        margin-top: 60px;    }    .cross-sells{        float: left;        margin-right: 30px;        @include calc( width, '( 100% - 30px ) / 2' );        >h2{            font-size: 20px;        }        +.cart_totals{            @include calc( width, '( 100% - 30px ) / 2' );        }    }    .cart_totals{        @extend .flw;        >h2{            font-size: 20px;        }        .checkout-button{            float: right;            color: #fff;            height: 40px;            line-height: 40px;            padding: 0 30px;            &:hover{                opacity: 0.8;            }        }    }    .woocommerce-shipping-calculator{        button[type="submit"]{            float: left;            height: 40px;            margin-bottom: 10px;            padding: 0 15px;            color: #fff;            border: 0;            font-size: 13px;            @extend .duration;            &:disabled{                opacity: 0.3;                pointer-events: none;            }            &:hover{                opacity: 0.8;            }        }    }    .woocommerce-cart-form__contents{        @media ( max-width: #{ $screen-sm - 1 } ) {            tr{                border-bottom: 0;            }            thead{                display: none;            }            td{                display: block;                clear: both;                padding: 10px 15px;                width: 100% $i;                border: 0;                background: #fdfdfd;                &:nth-child(even){                    background: #fafafa;                }            }            .product-quantity{                padding: 0;            }            .quantity{                width: 100%;                .qty{                    width: 100%;                    background: $tr;                    border: 0;                }            }            .product-remove{                background: #f3f3f3;                .remove{                    float: none;                    width: 30px;                    height: 30px;                    color: #f00                }            }            .quantity{                float: none;            }            .product-subtotal{                text-align: left;            }            .variation{                dt, dd{                    float: left;                    margin: 0 !important;                    width: 50%;                }            }            .actions{                padding-left: 0;                padding-right: 0;                .coupon{                    width: 100%;                    input{                        width: 100%;                        margin-right: 0;                    }                }                .cart-empty-btn{                    float: left;                    margin-right: 0;                    font-size: 12px;                }                button[type="submit"]{                    width: 100%;                    margin-top: 10px;                }            }        }    }}@media ( max-width: #{ $screen-sm - 1 } ) {    .cart_totals{        .shop_table_responsive{            th, td{                display: block;                border-left: 0;                border-right: 0;            }            th{                font-weight: 600;            }            tr{                &:first-child{                    th{                        border-top: 0;                    }                }                &:nth-child(odd){                    td{                        border-bottom: 0;                    }                }                &:nth-child(even){                    td{                        border-bottom: 0;                    }                }            }        }        .wc-proceed-to-checkout{            a{                width: 100%;                text-align: center;            }        }    }}