.has-menu-layout-6{
	.menu-open{
		#theme-menu-pusher{
			@include translate3d( -280px, 0, 0 );
			@media ( min-width: $screen-sm ) {
				@include translate3d( -345px, 0, 0 );
			}
		}
		.menu-layout-6{
			&#sidebar-menu-content{
				@media ( max-width: $screen-sm - 1 ) {
					width: 280px;
				}
				@include translate3d( 0, 0, 0 );
			}
			.menu-toggle-btn{
				span{
					@include rotate( 0 );
				}
			}
		}
	}
}
.menu-layout-6{
	&#sidebar-menu-content{
		@include translate3d( 100%, 0, 0 );
		left: auto;
		left: $ini;
		right: 0;
	}

	.header-container{
		@include flex( flex, space-between, center );
	}
	.m-col{
		@media ( min-width: $screen-md ) {
			max-width: 170px;
    		flex-basis: 170px;
    	}
    	.search-field{
    		background-color: #f3f3f3;
    	}
	}
	.content-center{
		flex-grow: 1;
		text-align: center;
		@extend .flex;
	}
	.header-logo{
		margin: 0 20px;
	}
	.menu-toggle-btn{
		margin-left: 10px;
	}
	
	@media ( min-width: $screen-md ) {
		.layout-6-sidebar-menu{
			display: none;
		}
	}
	@media ( max-width: $screen-md - 1 ) {
		.header-container{
			min-height: 70px;
		}
		.m1-col {
			display: none;
		}
		.content-center{
			justify-content: flex-start;
			.theme-primary-menu{
				display: none;
			}
			.header-logo{
				margin: 0;
			}
		}
	}
	@media ( max-width: 600px ) {

	}
}