.woocommerce-thankyou-order-received{
    display: block;
    clear: both;
}

.woocommerce-thankyou-order-details{
    padding-left: 0;
    background: #f8f8f8;
    margin-bottom: 50px;
    li{
        list-style: none;
        border-bottom: 1px dotted #e3e3e3;
        padding: 15px;
        &:last-of-type{
            border-bottom: 0;
        }
    }
    strong{
        display: block;
        font-size: larger;
    }
}

.woocommerce-table--order-details{
    margin-bottom: 50px;
    .wc-item-meta{
        padding-left: 0;
        margin: 0;
        li{
            list-style: none;
            +li{
                margin-top: 10px;
            }
            p{
                display: inline-block;
            }
        }
    }
    tr{
        &:nth-child( 2n ) {
            td{
                background: #fdfdfd;
            }
        }
    }
    th{
        background: #f8f8f8;
        font-weight: 600;
    }
    td{
        background: #fbfbfb;
        padding: 15px;
    }
    @media ( max-width: #{ $screen-sm - 1 } ) {
        th, td{
            display: block;
            border-right: 0;
            border-top: 0;
            border-left: 0
        }
        tr{
            &:last-of-type{
                td{
                    &:last-of-type{
                        border-bottom: 0
                    }
                }
            }
        }
    }
}