.comment-reply-title {    font-weight: normal;}#respond {    @extend .flw;}#commentform {    .comment-notes {        margin-bottom: 30px;    }    #comment {        @extend .flw;        border: 1px solid $tr;        background: #f6f6f6;        min-height: 240px;        padding: 20px;        resize: none;        margin-bottom: 30px;        @extend .duration;        &:focus {            border-color: #ddd;        }    }    #email {        margin-right: 30px;    }    #author,    #email {        height: 60px;        padding: 0 20px;        border: 1px solid $tr;        background: #f6f6f6;        max-width: 100%;        float: left;        margin-bottom: 30px;        @include calc(width, '( 100% - 30px ) / 2');        @extend .duration;        &:focus {            border-color: #ddd;        }    }    .form-submit {        margin-bottom: 0;    }    #submit {        min-width: 200px;        height: 55px;        padding: 0 15px;        border: 0;        color: #fff;        background-color: #333333;        font-size: 15px;        font-family: 'Eina03';        @extend .duration;        &:hover {            opacity: 0.8;        }    }}