/*GENERAL
******************************************************************************************************/
.shop-content{
    >.product{
        position: relative;
        clear: both;
        max-width: 1440px;
        margin: 0 auto;

        &:after{
        	clear: both;
        	display: block;
        	content: '';
        }
    }
}

.wrapper-product-content {
	&:after{
    	clear: both;
    	display: block;
    	content: '';
    }
}

/* PRODUCT */
.woocommerce-loop-product__link{
    .star-rating{
        margin-bottom: 20px;
    }
}

/* COMMENT FORM */
.comment-form-rating{
    .stars{
        &.selected{
            span{
                &:hover{
                    a{
                        color: #ffc400;
                        &:before{
                            content: "\f4b3" $i;
                        }
                        &.active{
                            ~a{
                                &:before{
                                    content: "\f4b2";
                                }
                            }
                        }
                    }
                }
                a{
                    color: #ffc400;
                    &:before{
                        content: "\f4b3";
                    }
                    &.active{
                        ~a{
                            &:before{
                                content: "\f4b2";
                            }
                        }
                    }
                    &:hover{
                        ~a{
                            &:before{
                                content: "\f4b2" $i;
                            }
                        }
                    }
                }
            }
        }
        span{
            &:hover{
                a{
                    color: #ffc400;
                    &:before{
                        content: "\f4b3";
                    }
                }
            }
            a{
                font-size: 0;
                @include duration( 0s );
                &:hover{
                    &:before{
                        content: "\f4b3";
                    }
                    ~a{
                        &:before{
                            content: "\f4b2";
                        }
                    }
                }
                &:before{
                    font-size: 18px;
                    font-family: $ion;
                    content: "\f4b2";
                }
            }
        }
    }
}



/* PRODUCT IMAGES
******************************************************************************************************/

/* VIDEO BUTTON */
.single-product-gallery{
    &:hover{
        .video-popup-btn{
            opacity: 1;
        }
    }
}
.video-popup-btn{
    opacity: 0;
    position: absolute;
    z-index: 2;
    @extend .flex;
    &:before{
        font-size: 20px;
        margin-right: 10px;
    }
}

/* IMAGE ZOOM */
.ez-zoom{
    position: relative;
}
.easyzoom-flyout {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    opacity: 0;
    z-index: 1;
    overflow: hidden;
    background: #FFF;
    @include transition( opacity 0.5s ease );
    &:hover{
        opacity: 1;
    }
    img{
        max-width: $ini $i
    }
}

/* GALLERY SLIDER */
.single-gallery-slider{
    .single-product-gallery{
        position: relative;
        min-height: 1px;
        float: left;
        +.summary{
            padding-left: 50px;
        }
    }
    .video-popup-btn{
        right: 20px;
    }
    .pro-thumb{
        cursor: pointer;
        position: relative;
        &.tns-nav-active{
            img{
                border-color: #999;
            }
        }
        img{
            border: 1px solid $tr;
        }
    }
    .pro-carousel-image{
    	@media ( min-width: $screen-md ) {
	        .tns-controls{
	            button{
            		opacity: 0;
            		@include duration( 0.3s );
	                &[data-controls="prev"]{
	                    left: 0;
	                }
	                &[data-controls="next"]{
	                    right: 0;
	                }
            	}
            }
            &:hover{
	            .tns-controls{
	                button{
	                    opacity: 1;
	                    &[data-controls="prev"]{
	                        left: 20px;
	                    }
	                    &[data-controls="next"]{
	                        right: 20px;
	                    }
	                }
	            }
	        }
        }
        @media ( max-width: $screen-md - 1 ) {
        	.tns-controls{
	            button{
	        		opacity: 1;
	                &[data-controls="prev"]{
	                    left: 20px;
	                }
	                &[data-controls="next"]{
	                    right: 20px;
	                }
	        	}
	        }
        }
    }

    .pro-carousel-thumb{
    	.tns-controls{
    		button{
    			opacity: 0;
    			visibility: hidden;
    			width: 30px;
    			height: 30px;
    			@extend .duration;
    		}
    	}
    	&:hover{
    		.tns-controls{
	    		button{
	    			opacity: 1;
	    			visibility: visible;
	    		}
	    	}
    	}
    }
    &.single-gallery-vertical{
        .single-product-gallery{
            @media ( min-width: #{ $screen-lg } ) {
                width: 700px;
                +.summary{
                    @include calc( width, '100% - 700px' );
                }
                &:not(.has-product-thumbnails){
                    width: 600px;
                    .pro-carousel-image{
                        width: 100%;
                    }
                    +.summary{
                        @include calc( width, '100% - 600px' );
                    }
                }
            }
        }
        .pro-carousel-thumb{
        	float: left;
        	max-width: 100%;
        	@media ( min-width: $screen-md ) {
        		width: 80px;
            	margin-right: 20px;
        	}
            .tns-controls {
            	[data-controls="prev"]{
            		top: 20px;
            		left: 50%;
            		transform: translateX( -50% ) translateY( 0 ) rotate( 90deg );
            	}
            	[data-controls="next"]{
					right: auto;
					top: auto;
					left: 50%;
					bottom: 20px;
					transform: translateX( -50% ) translateY( 0 ) rotate( 90deg );
            	}
            }
        }
        .pro-thumb{
            margin-bottom: 10px;
        }
        .has-product-thumbnails{
        	.pro-carousel-image{
	        	@media ( min-width: $screen-md ) {
	        		float: right;
		            @include calc( width, '100% - 100px' );
	        	}
	        	@media ( max-width: $screen-md - 1 ) {
	        		margin-bottom: 10px;
	        	}
	        }
        }
        .shop-content > .this-product-has-gallery-image{
            >.onsale{
                left: 120px;
                top: 20px;
            }
        }
        .video-popup-btn{
            bottom: 20px;
        }
    }

    &.single-gallery-horizontal{
        .single-product-gallery{
            @media ( min-width: #{ $screen-lg } ) {
                width: 600px;
                +.summary{
                    @include calc( width, '100% - 600px' );
                }
            }
            &:not(.has-product-thumbnails){
                .video-popup-btn{
                    bottom: 20px;
                }
            }
        }
        .video-popup-btn{
            bottom: 110px;
        }
        .has-product-thumbnails{
        	.pro-carousel-thumb{
	            margin-top: 10px;
	            @extend .flw;
	        }
        }
        .pro-carousel-image{
            @extend .flw;
        }
    }
	
	.has-product-thumbnails{
		@media ( max-width: $screen-md - 1 ) {
			.pro-thumb{
				max-width: 100px;
			}
		}

		#gallery-image {
			&:not(.tns-slider) {
				.pro-img-item{
					&:first-of-type {
						@extend .circle-loading;
						img {
							opacity: 0;
						}
						~ .pro-img-item{
							display: none;
						}
					}
				}
			}	  		
		}

		#gallery-thumb {
			&:not(.tns-slider) {
				.pro-thumb{
					&:first-of-type {
						@extend .circle-loading;
						img {
							opacity: 0;
						}
						~ .pro-thumb{
							display: none;
						}
					}
				}
			}	  		
		}
	}

    .summary{
        @extend .flw;
    }
}

/* GALLERY IMAGE */
.single-gallery-image{
    .single-product-gallery{
        position: relative;
        float: left;
        @media ( min-width: #{ $screen-lg } ) {
            margin-right: 50px;
        }
    }
    .video-popup-btn{
        top: 15px;
        right: 20px;
    }

    .single-product-gallery {
    	@media ( min-width: 992px ) {
        	.pro-img-item {
        		float: left;
        		margin-bottom: 30px !important;
        	}
        }
    }

    &.single-gallery-list{
        .single-product-gallery{
            @media ( min-width: #{ $screen-lg } ) {
                width: 600px;
                +.summary{
                    @include calc( width, '100% - 650px' );
                }
            }
        }
    }
    &.single-gallery-grid{
    	@media (max-width: 1800px) {
			.cart {
				.single_add_to_cart_button {
					width: auto;
					padding: 0 30px;
				}
			}
		}
        .single-product-gallery{
        	&.has-product-thumbnails{
				@media ( min-width: $screen-md ) {
					.pro-img-item {
						@include calc(max-width, '( 100% - 30px ) / 2');
						&:nth-child(odd) {
							clear: both;
							margin-right: 30px;
						}
					}
				}
			}
			.pro-img-item {
				max-width: 100%;
				&:first-of-type:last-of-type{
					margin: 0 $i;
				}
			}
            @media ( min-width: 1600px ) {
                width: 930px;
                +.summary{
                    @include calc( width, '100% - 980px' );
                }
                &:not(.has-product-thumbnails){
                    width: 600px;
                    +.summary{
                        @include calc( width, '100% - 650px' );
                    }
                    .pro-img-item{
                        max-width: 100%;
                        margin-right: 0;
                    }
                }

                .pro-img-item{
                    max-width: 450px;
                    &:nth-child(odd){
                        clear: both;
                        margin-right: 30px;
                    }
                }
            }
            @media ( min-width: $screen-md ) and ( max-width: 1599px ) {
            	&.has-product-thumbnails{
            		width: 100%;
            		margin-right: 0;
				}
				&:not(.has-product-thumbnails) {
					width: 50%;
					+.summary.entry-summary{
						@include calc( width, '50% - 50px' );
					}
				}
            }
        }
    }
    .summary{
        float: left;
    }

    .has-product-thumbnails{
    	@media ( max-width: $screen-md - 1 ) {
    		#gallery-image {
				&:not(.tns-slider) {
					.pro-img-item{
						&:first-of-type {
							&:before{
								@include loading();
							}
							img {
								opacity: 0;
							}
							~ .pro-img-item{
								display: none;
							}
						}
					}
				}
			}
    	}
    }
}

figure.pro-img-item{
	margin: 0;
	cursor: pointer;
}

.pro-img-item{
    position: relative;
    &.image-is-loading{
        @extend .circle-loading;
    }
}

/* For Elementor Theme Builder */
.zoa-product-images-widget{
	.single-product-gallery{
		width: 100% $i;
	}
}


/* SUMMARY
******************************************************************************************************/
.product-type-grouped{
    .summary{
        .yith-wcwl-add-to-wishlist{
            margin-top: 0;
        }
        .group_table{
            margin-top: 0;
            border-left: 0;
            border-right: 0;
            td{
                border-left: 0;
                border-right: 0;
                padding-left: 0;
                padding-right: 0;
            }
            .loop-action{
                position: static;
                transform: none;
            }
            .loop-action{
                justify-content: flex-start;
                .product-quick-view-btn,
                .zoa-add-to-cart-btn,
                .added_to_cart,
                .yith-wcwl-add-to-wishlist a{
                    opacity: 1;
                    visibility: visible;
                    transform: none;
                }
                .yith-wcwl-add-to-wishlist{
                    border-bottom: 0;
                    transform: none;
                    a{
                        justify-content: center;
                        font-size: 0;
                        &:before{
                            margin-right: 0;
                        }
                    }
                }
            }
        }
    }
}

.product-type-variable{
    .variations{
        border: 0;
        margin-top: 0;
        td{
            @extend .flw;
            border: 0;
            padding: 0;
        }
        tr{
            @extend .flw;
            display: block;
            +tr{
                margin-top: 21px;
            }
        }
    }
    .woocommerce-variation-price{
        margin-bottom: 20px;
        padding-bottom: 15px;
        border-bottom: 1px solid #f8f5f5;
    }
}
.product-type-external{
    .summary{
        .yith-wcwl-add-to-wishlist{
            margin-top: 0;
        }
    }
}

.summary{
    .product_title{
        font-size: 36px;
        margin-top: 35px;
        +.price{
            margin-top: 15px;
        }
    }
    .yith-wcwl-add-to-wishlist{
        margin-top: 15px;
        border-bottom: 1px solid;
        a{
            @include flex( flex, space-between, center );
            font-size: 15px;
            &:before{
                min-width: 20px;
                margin-right: 5px;
            }
            &.adding-to-wishlist{
                position: relative;
            }
        }
        .yith-wcwl-wishlistaddedbrowse{
            a{
                &:before{
                    height: 20px;
                    @extend .flex;
                }
            }
        }
    }
}
.woocommerce-product-rating{
    margin: 10px 0;
    .star-rating{
        position: relative;
        top: 2px;
    }
}
.woocommerce-product-details__short-description{
    margin-top: 10px;
    margin-bottom: 20px;
    font-size: 15px;
}
.cart{
    .single_add_to_cart_button{
        width: 380px;
        height: 55px;
        color: #fff;
        border: 0;
        margin-left: 10px;
        text-transform: uppercase;
        font-size: 15px;
        //position: relative;
        @extend .duration;
        &:hover{
            opacity: 0.7;
        }
        &:before{
            content: "\e904";
            font-family: 'zoa';
            margin-right: 18px;
        }
        &.disabled{
            cursor: default;
            pointer-events: none;
            opacity: 0.5;
        }
    }
    .yith-wcwl-add-to-wishlist{
        float: left;
        clear: both;
        margin-top: 15px;
    }
}

.quantity{
    width: 130px;
    height: 55px;
    float: left;
    &:not(.hidden){
        position: relative;
        .modify-qty{
            cursor: pointer;
            position: absolute;
            font-family: $ion;
            font-size: 30px;
            width: 35px;
            height: 100%;
            top: 50%;
            @include translatey( -50% );
            &[data-click=minus]{
                left: 0;
                text-align: right;
                padding-right: 5px;
                @include flex( flex, flex-end, center );
                &:before{
                    content: "\f462"
                }
            }
            &[data-click=plus]{
                right: 0;
                text-align: left;
                padding-left: 5px;
                @include flex( flex, flex-start, center );
                &:before{
                    content: "\f489"
                }
            }
        }
        .qty{
            height: 100%;
            width: 100%;
            text-align: center;
            padding: 0 35px;
            font-size: 18px;
            -moz-appearance:textfield;
            &::-webkit-inner-spin-button, 
            &::-webkit-outer-spin-button{
                -webkit-appearance: none;
                -moz-appearance: none;
                appearance: none;
            }
        }
    }
}
.product_meta{
    @extend .flw;
    border-top: 1px solid #f8f5f5;
    padding-top: 30px;
    margin-top: 50px;
    span{
        font-size: 15px;
    }
    >span{
        @extend .flw;
        font-weight: bold;
        line-height: 35px;
        > span,
        > a{
            font-weight: normal;
        }
    }
}
.p-shared{
    margin-top: 44px;
    span{
        display: block;
        line-height: 24px;
    }
    a{
        margin-right: 18px;
        font-size: 17px;
    }
}


/* TABS
******************************************************************************************************/
.woocommerce-tabs{
    max-width: 770px;
    margin: 0 auto;
    clear: both;
    .tabs{
        @extend .flw;
        margin-top: 100px;
        margin-bottom: 55px;
        padding-left: 0;
        list-style: none;
        text-align: center;
        li{
            display: inline-block;
            padding: 0 30px;
        }
        a{
            border-bottom: 2px solid $tr;
            font-size: 18px;
            font-weight: 600;
            padding: 5px 0;
        }
    }
    .woocommerce-Tabs-panel{
        @extend .flw;
        margin-bottom: 120px;
        display: none;
    }
}
.woocommerce-Reviews{
    .woocommerce-Reviews-title,
    .woocommerce-review__dash{
        display: none;
    }
    .commentlist{
        list-style: none;
        padding-left: 0;
        margin: 0;
    }
    .comment_container{
        @extend .flw;
        margin-bottom: 50px;
        padding-bottom: 50px;
        border-bottom: 1px solid #eeeeee;
        img{
            float: left;
            border-radius: 50%;
            max-width: 60px;
            +.comment-text{
                @include calc( width, '100% - 80px' );
                margin-left: 20px;
            }
        }
        .comment-text{
            @extend .flw;
            margin-top: 0;
        }
        .woocommerce-review__author{
            font-size: 16px;
            font-weight: 600;
        }
        .woocommerce-review__published-date{
            display: block;
            font-size: 12px;
        }
    }

    .meta{
        margin-bottom: 15px;
    }

    #reply-title,
    .comment-form-rating label{
        font-weight: 700;
    }

    #commentform{
        .comment-form-author,
        .comment-form-email{
            float: left;
            @include calc( width, '( 100% - 30px ) / 2' );
            #author,
            #email{
                width: 100%;
            }
        }
        .comment-form-email{
            margin-left: 30px;
        }
        .required{
            color: #f00
        }
    }
}

/* RELATED && UPSELL
******************************************************************************************************/
.upsells.products,
.related.products{
    @extend .flw;
    border-top: 1px solid #eeeeee;
    padding-top: 76px;
    > h2{
        text-align: center;
        font-size: 36px;
        font-weight: normal;
        margin-bottom: 63px;
    }
}

/* Hide breadcrumb layout-2 on mobile */
@media ( max-width: 991px ) {
	.page-header{
		#theme-bread{
			display: none;
		}
	}
}

/* ELEMENTOR WOOCOMMERCE WIDGET */
.elementor-add-to-cart {
    .cart {
        display: block !important;
    }
}