.variations {	select {		float: left;	}	.label {		font-weight: 600;		text-transform: uppercase;		font-size: 13px;		margin-bottom: 7px;	}	tr:last-of-type {		.label {			margin-bottom: 13px;		}	}}.swatch {	width: 25px;	height: 25px;	border-radius: 50%;	display: inline-block;	cursor: pointer;	position: relative;	img {		border-radius: 50%;	}	&:before {		content: '';		position: absolute;		left: -4px;		top: -4px;		right: -4px;		bottom: -4px;		border: 1px solid #ddd;		border-radius: 20px;	}	+ .swatch {		margin-left: 15px;	}	&:not(.swatch-label) {		font-size: 0;	}	&.swatch-label {		@extend .inline-flex;		width: auto;		min-width: 25px;		padding: 0 5px;	}}.reset_variations {	margin-top: 15px;	float: left;	&:before {		content: "\f49a";		font-family: $ion;		font-size: 25px;		position: relative;		top: 4px;		margin-right: 5px;		display: inline-block;	}}.pro-swatch-list {	margin-top: 10px;	@extend .flw;	.p-attr-image {		img {			border-radius: 50%;			max-height: 100%;		}	}}.p-attr-swatch {	width: 24px;	height: 24px;	border-radius: 50%;	margin: 0 5px;	cursor: pointer;	position: relative;	@extend .inline-flex;	&:before {		position: absolute;		content: '';		left: -3px;		top: -3px;		right: -3px;		bottom: -3px;		border: 1px solid $tr;		border-radius: 50%;		@extend .duration;	}	&.p-attr-color {		width: 20px;		height: 20px;	}	&.p-attr-label{		width: auto;		height: auto;		border-radius: 20px;		border: 1px solid #ddd;		display: inline-block;		font-size: 12px;		padding: 0 10px;		margin-bottom: 5px;		&:before{			content: none		}	}	&.active {		&:before {			border-color: #dedede;		}	}}// swatch tooltip product.tawcvs-swatches .swatch span {	display: none;}