/* loading animation */@include keyframes( circle-loading ){    0%{ @include rotate(0) }    100%{ @include rotate(360) }}@mixin loading( $size: 20px, $color: #222, $base_color: #ddd, $border_size: 1px ){    content: '' $i;    z-index: 2;    position: absolute;    border: $border_size solid $base_color;    border-top-color: $color;    border-radius: 100%;    height: $size;    width: $size;    top: 50%;    left: 50%;    @include calc( margin-left, '-#{$size} / 2' );    @include calc( margin-top, '-#{$size} / 2' );    @include animation( circle-loading 0.35s linear infinite );}.circle-loading{    &:before{        @include loading();    }}