.footer-subscribe-form{    @extend .flw;    input{        float: left;        height: 50px;        padding: 0 20px;        border-color: #ddd;        @extend .duration;        @include calc( width, '100% - 180px' );        &:focus{            border-color: #999;        }    }    button{        width: 180px;        float: left;        height: 50px;        padding: 0 15px;        border: 0;        color: #fff;        @extend .duration;        &:hover{            opacity: 0.8;        }    }}