.blog-article {
  @extend .flw;
  margin-bottom: 35px;
}
.blog-header-info {
  .sticky & {
    &:after {
      content: '\f2a6';
      font-family: $ion;
      margin-left: 15px;
      font-size: 30px;
      display: inline-block;
      @include rotate(30);
    }
  }
  .if-item {
    text-transform: uppercase;
    &:after {
      content: '/';
      margin: 0 12px;
    }
    &:last-child {
      &:after {
        content: none;
      }
    }
    a {
      &:hover {
        text-decoration: underline;
      }
    }
  }
}
.blog-read-more {
  font-size: 15px;
  border-bottom: 2px solid;
  padding-bottom: 5px;
  display: inline-block;
  margin-top: 10px;
}

.post-layout-classic,
.post-layout-overlap {
  margin-bottom: 30px;

  .entry-title {
    font-size: 20px;
  }
}

.post-layout-overlap {
  .read-more {
    display: flex;
    align-items: center;
    margin-top: 10px;
    font-size: 13px;
  }

  .read-more-icon {
    display: inline-block;
    margin-right: 10px;
    font-size: 24px;
    color: #222222;
  }
}
