/* container */.shop-container {	padding: 0 15px;	clear: both;	@media (min-width: 1380px) {		padding-left: 105px;		padding-right: 105px;		width: auto;	}	@media (min-width: #{ $screen-md }) {		&.with-left-sidebar, &.with-right-sidebar {			@include flex(flex, space-between, flex-start);			.shop-content {				@include calc(width, '100% - 400px');				@media (max-width: 1600px) {					@include calc(width, '100% - 320px');				}			}			.shop-sidebar {				width: 300px;				@media (max-width: 1600px) {					width: 270px;				}			}		}		&.with-left-sidebar {			.shop-content {				order: 2			}			.shop-sidebar {				order: 1			}		}	}}.shop-content {	position: relative;}/* columns */.products {	clear: both;	padding-left: 0;	margin: 0;	.product {		text-align: center;		list-style: none;		float: left;		margin: 0 30px 56px 0;		position: relative;		@media (min-width: #{ $screen-md }) {			@include calc(width, '( 100% - 90px ) / 4');		}		@media (min-width: #{ $screen-ios }) and (max-width: #{ $screen-md - 1 }) {			@include calc(width, '( 100% - 30px ) / 2');			&:nth-of-type( 2n) {				margin-right: 0;			}			&:nth-of-type( 2n + 3) {				clear: both;			}		}		/*ios*/		@media (max-width: #{ $screen-ios - 1 }) {			width: 100%;			clear: both;		}		@media (min-width: 1200px) {			&:hover {				.product-quick-view-btn, .zoa-add-to-cart-btn, .added_to_cart, .yith-wcwl-add-to-wishlist a {					opacity: 1;					visibility: visible;				}				.product-quick-view-btn, .zoa-add-to-cart-btn, .yith-wcwl-add-to-wishlist, .added_to_cart {					@include translatey(-20px);				}			}		}		/* label plugin */		.label-wrap{			position: static;		}	}	&.columns-1 {		.product {			width: 100%;			margin-right: 0;		}	}	@media (min-width: #{ $screen-md }) {		@for $i from 2 through 6 {			&.columns-#{ $i } {				.product {					@include calc(width, '( 100% - ( 30px * #{ $i - 1 } ) ) / #{ $i }');					&:nth-of-type( #{ $i }n + #{ $i + 1 }) {						clear: both;					}					&:nth-of-type( #{ $i }n ) {						margin-right: 0;					}				}			}		}	}}/* sidebar */.shop-sidebar {	min-height: 1px;	.woocommerce-widget-layered-nav-dropdown__submit {		padding: 6px 17px;		border: 0;		color: #fff;		background: #ebe9eb;		border-radius: 3px;		font-size: 12px;		margin-top: 10px;		&:hover {			opacity: 0.8;		}	}	.tagcloud {		a {			line-height: 26px;		}	}}/* top bar */.shop-top-bar {	@include flex(flex, space-between, center);	margin-bottom: 30px;	.woocommerce-result-count {		margin-bottom: 25px;	}	.woocommerce-ordering {		margin-bottom: 25px;		select {			border: 0;			border-bottom: 1px solid #eeeeee;			height: 40px;			font-size: 14px;		}	}}/* content-product */.woocommerce-loop-product__title {	font-size: 14px;	font-weight: normal;	margin-bottom: 11px;}.onsale {	position: absolute;	z-index: 2;	padding: 0 5px;	left: 15px;	top: 15px;	font-size: 11px;	min-width: 50px;	height: 18px;	background: #fff;	font-weight: 600;	pointer-events: none;	@extend .flex;}.price {	display: block;	> .amount, ins {		font-weight: 700;	}	ins {		margin-left: 13px;	}}.product-image-wrapper {	position: relative;	&:hover {		.hover-product-image {			opacity: 1;			pointer-events: auto;			visibility: visible;		}	}	&.image-is-loading {		@extend .circle-loading;	}}.hover-product-image {	position: absolute;	left: 0;	top: 0;	right: 0;	bottom: 0;	background-size: cover;	opacity: 0;	visibility: hidden;	pointer-events: none;	background-repeat: no-repeat;	background-position: center center;	@extend .duration;}.loop-action {	width: 100%;	position: absolute;	left: 50%;	bottom: 10px;	height: 35px;	pointer-events: none;	@extend .flex;	@include translatex(-50%);	@media (min-width: 1200px) {		bottom: 0;		height: 45px;	}	.product-quick-view-btn, .zoa-add-to-cart-btn, .added_to_cart, .yith-wcwl-add-to-wishlist a {		width: 35px;		height: 35px;		color: #fff;		background: #222222;		border-radius: 50%;		pointer-events: auto;		@extend .inline-flex;		&:before {			color: #fff;		}		@media (min-width: 1200px) {			width: 45px;			height: 45px;			opacity: 0;			visibility: hidden;		}	}}@media (max-width: 991px) {	.loop-action {		.product-quick-view-btn, .zoa-add-to-cart-btn, .added_to_cart, .yith-wcwl-add-to-wishlist a {			opacity: 0;			visibility: hidden;		}	}	.loop-action--visible-on-mobile {		.product-quick-view-btn, .zoa-add-to-cart-btn, .added_to_cart, .yith-wcwl-add-to-wishlist a {			opacity: 1;			visibility: visible;		}	}}.yith-wcwl-add-to-wishlist {	.feedback {		display: none;	}	.loop-action & {		a {			font-size: 0;		}	}}.product-quick-view-btn {	cursor: pointer;	margin-right: 10px; //@include translatey(20px);	@include duration(0.2s);}.zoa-add-to-cart-btn, .added_to_cart {	font-size: 0;	@include duration(0.4s);	&:before {		content: "\e904";		font-family: zoa;		font-size: 14px;	}}.zoa-add-to-cart-btn {	&.added {		display: none;		+ .added_to_cart {			&:before {				content: "\f3fd";				font-family: $ion;				font-size: 30px;			}		}	}}.added_to_cart {	z-index: 2;}/* updating cart */.ajax_add_to_cart {	position: relative;	&.loading {		@extend .circle-loading;	}}/* adding to wishlist */.adding-to-wishlist {	&.add_to_wishlist {		@extend .circle-loading;		&:before {			position: static;			margin-left: 0;			margin-top: 0;		}	}}/* PRODUCT LABEL */.zoa-product-label {	position: absolute;	top: 15px;	right: 15px;	z-index: 5;	padding: 0 10px;	pointer-events: none;	height: 18px;	font-size: 11px;	line-height: 18px;	&.sold-out-label {		background-color: #999;		color: #fff	}}.has-default-loop-add-to-cart-button{	.product {		.button {			padding: 0.4em 2em;			border: 1px solid #e3e3e3;			display: inline-block;			margin-top: 20px;		}		.added_to_cart {			&:before{				content: none;			}			font-size: 14px;			padding: 0.4em 2em;			border: 1px solid #e3e3e3;			display: inline-block;			transform: none !important;		}	}}