#comments{
    @extend .flw;
    .navigation{
        margin-top: 20px;
        margin-bottom: 30px;
        @extend .flw;
    }
    .cmt-previous{
        float: left;
        a{
            &:before{
                content: "\f100";
                font-family: $awe;
                margin-right: 5px;
            }
        }
    }
    .cmt-next{
        float: right;
        a{
            &:after{
                content: "\f101";
                font-family: $awe;
                margin-left: 5px;
            }
        }
    }
}
.comments-title{
    font-weight: normal;
    +.comment-list{
        margin-top: 30px;
    }
}
.comment-list{
    @extend .flw;
    border-top: 1px solid #eeeeee;
    padding-top: 30px;
    .children{
        @extend .flw;
        margin: 0;
        #respond{
            margin-bottom: 50px;
        }
    }
    #respond{
        border-bottom: 1px solid #eee;
        padding-bottom: 50px;
        margin-bottom: 50px;
        @media ( min-width: 992px ){
            float: right;
            @include calc( width, '100% - 130px' );
        }
    }
}
.comment-item{
    @extend .flw;
    padding-bottom: 40px;
    margin-bottom: 40px;
    border-bottom: 1px solid #eeeeee;
}
.comment-avatar{
    width: 100px;
    float: left;
    margin-right: 30px;
    img{
        border-radius: 50%;
    }
}
.comment-content{
    float: left;
    max-width: 100%;
    @include calc(width, '100% - 130px')
}
.comment-author{
    float: left;
}
.comment-author-name{
    font-weight: 600;
    font-size: 16px;
}
.comment-info{
    float: right;
    a{
        text-transform: uppercase;
        font-size: 11px;
    }
}
.comment-reply-link{
    &:before{
        content: "\f075";
        font-family: $awe;
        margin-right: 7px;
    }
}
.comment-edit-link{
    margin-left: 30px;
    &:before{
        content: "\f040";
        font-family: $awe;
        margin-right: 7px;
    }
}
.comment-time{
    display: inline-block;
    margin-left: 30px;
    text-transform: uppercase;
    font-size: 12px;
    font-weight: normal;
    &:before{
        content: "\f017";
        font-family: $awe;
        margin-right: 7px;
    }
}
.comment-text{
    @extend .flw;
    margin-top: 10px;
}