#page-loader{
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    z-index: 9999998;
    overflow: hidden;
    height: 2px;
    @media ( max-width: #{ $screen-md - 1 } ) {
        display: none;
    }
}
#theme-container{
    background: #fff;
    position: relative;
    overflow-x: hidden;
    @include transition( all 0.8s cubic-bezier( 0.77, 0, 0.175, 1 ) );
    .is-page-loading &{
        opacity: 0.2;
        &.is-ready{
            opacity: 1;
            +.is-loading-effect{
                opacity: 0;
                visibility: hidden;
            }
        }
        &.is-loading{
            opacity: 0.2;
            +.is-loading-effect{
                pointer-events: auto;
                opacity: 1;
                visibility: visible;
            }
        }
    }
}
.is-loading-effect{
    position: fixed;
    z-index: 99999999;
    left: 50%;
    top: 50%;
    width: 40px;
    height: 40px;
    margin-left: -20px;
    margin-top: -20px;
    pointer-events: none;
    @include duration( 1s );
    &:before{
        @include loading( $size: 40px, $color: #222, $base_color: #ddd, $border_size: 2px );
    }

    .page-template-elementor_canvas & {
        opacity: 0;
        visibility: hidden;
    }
}
.is-content-ready{
	.is-loading-effect{
		opacity: 0;
        visibility: hidden;
	}
}