$star: "\f2fc\f2fc\f2fc\f2fc\f2fc";$num: 16px;.star-rating{    display: inline-block;    width: 63.13px;    height: $num;    position: relative;    line-height: $num;    &:before{        font-family: $ion;        font-size: $num;        content: $star;        white-space: nowrap;        position: absolute;        left: 0;        top: 0;        opacity: 0.25;    }    >span{        font-size: 0;        position: absolute;        left: 0;        top: 0;        overflow: hidden;        white-space: nowrap;        line-height: $num;        &:before{            font-family: $ion;            font-size: $num;            content: $star;            color: #ffc400;            position: absolute;            left: 0;            top: 0;        }    }}.price,.cart{    mark,    ins{        background: none;    }}/* UL - RADIO INPUT */#shipping_method,.methods{    list-style: none;    padding: 0;    input{        &[type="radio"]{            display: none;            &:checked{                +label{                    font-weight: 700;                    &:before{                        content: "\f192";                    }                }            }            +label{                &:before{                    font-family: $awe;                    content: "\f10c";                    margin-right: 10px;                    font-size: 16px;                    color: #cccccc                }            }        }    }}/* FORM ROW */.form-row{    @extend .flw;    margin-bottom: 16px;    >label{        font-size: 13px;        margin-bottom: 2px;        display: block;        abbr{            text-decoration: none;        }    }    &.form-row-first{        margin-right: 20px;        @include calc( width, '( 100% - 20px ) / 2' );    }    &.form-row-last{        @include calc( width, '( 100% - 20px ) / 2' );    }    select{        @extend .flw;        height: 50px;        padding: 0 20px;        border-color: #f3f3f3;        background: #f9f9f9;    }    input[type="text"],    input[type="tel"],    input[type="email"],    input[type="password"]{        @extend .flw;        height: 50px;        padding: 0 20px;        border-color: #f3f3f3;        background: #f9f9f9;        @extend .duration;        &:focus{            border-color: #ddd;        }    }    textarea{        @extend .flw;        min-height: 240px;        padding: 20px;        border-color: #f3f3f3;        background: #f9f9f9;        resize: none;        @extend .duration;        &:focus{            border-color: #ddd;        }    }}/* LOGIN FORM */.woocommerce-form-login-toggle,.woocommerce-form-coupon-toggle{    display: block;    clear: both;    line-height: 40px;    text-align: center;    .woocommerce-info{    	font-weight: 700;    }    a{        text-transform: uppercase;        @media ( max-width: #{ $screen-md - 1 } ) {            display: inline-block;        }    }}.woocommerce-form-login-toggle{	.woocommerce-info{		&:before{			content: "\e912";			font-family: 'zoa';			margin-right: 10px;			font-size: 20px;			line-height: 1;			vertical-align: text-bottom		}	}}.woocommerce-form-coupon-toggle{	.woocommerce-info{		&:before{			content: "\f34b";			font-family: $ion;			margin-right: 10px;			font-size: 20px;		}	}}.woocommerce-form-coupon{	padding-top: 10px;	text-align: center;	margin: 0 auto;	+.woocommerce-checkout{		margin-top: 50px;	}}/* VARIATION */.shop_table{    .variation{        margin-bottom: 0;        font-size: 13px;        line-height: 18px;        dt{            font-weight: normal;            float: left;            margin-right: 5px;            clear: both;        }        dd{            margin-bottom: 0;            float: left;            p:last-child{                margin-bottom: 0;            }        }    }}/*  */.woocommerce{    /* INPUT CHECKBOX */    input[type="checkbox"]{        margin: 0;        &:not(.partial_pay_through_wallet){        	display: none;        }        &:checked{            +span{                &:before{                    content: "\f046";                }            }        }        +span{            font-size: 13px;            font-weight: 600;            line-height: 1;            &:not(.woocommerce-terms-and-conditions-checkbox-text){            	@include flex( flex, flex-start, center );            	flex-wrap: wrap;            	line-height: 1.6;            }            &:before{                font-weight: normal;                font-family: $awe;                content: "\f096";                margin-right: 10px;                font-size: 20px;                line-height: 0;            }        }    }    .woocommerce-form-login{    	padding: 10px 0;    	margin: 0 auto;        max-width: 50%;    }    .woocommerce-form__label.inline{        display: inline-block;        position: relative;        top: 2px;    }}/* WARNING */.woocommerce-message,.woocommerce-error,.woocommerce-info{    list-style: none;    padding-left: 0;}.woocommerce-error{    color: #b81c23}.woocommerce-info{    color: #1e85be}.woocommerce-message{    color: #8fae1b}.woocommerce-notice--success{    color: #339966}/* Product load more button */.has-product-load-more-button{	ul.products{		>.product{			transition: margin 1000ms;			opacity: 1;			margin-top: 0;			@include duration( 1.2s );			&.loaded{				margin-top: 20px;				opacity: 0;			}		}	}	.load-more-product-btn{		color: #fff;		padding: 15px;		min-width: 160px;		font-weight: 600;		position: relative;		@extend .inline-flex;		&:before{			content: '';			position: absolute;			right: 10px;			top: 50%;			margin-top: -10px;			width: 20px;			height: 20px;			border: 2px solid;			border-left-color: transparent;			border-radius: 50%;			opacity: 0;			transition-duration: 0.5s;			transition-property: opacity;			animation-duration: 1s;			animation-iteration-count: infinite;			animation-name: circle-loading;			animation-timing-function: linear;		}		&:after{			content: '';			display: inline-block;			height: 100%;			width: 0px;			transition-delay: 0.5s;			transition-duration: 0.75s;			transition-property: width;		}		&:hover{			color: #fff;			filter: grayscale( 10% );		}		&.loading{			pointer-events: none;  			cursor: not-allowed;  			&:before{				transition-delay: 0.5s;				transition-duration: 1s;				opacity: 1;  			}  			&:after{				transition-delay: 0s;				width: 20px;  			}		}	}}/* hide sticky button when quick-view open */.shop-buttons-is-sticky{	&.quick-view-open{		.single_add_to_cart_button{			@media ( max-width: 767px ) {				position: static;			}		}	}}/* ELEMENTOR PREVIEW MODE */.elementor-editor-active,.elementor-editor-preview {	.elementor-add-to-cart {		&.elementor-product-variable {			.value {				select {					position: static !important;				}				.reset_variations {					margin-top: -10px !important;				}			}		}	}}