.theme-primary-menu {
    &:not(.theme-sidebar-menu) {
        padding-left: 0;
        margin: 0;
        @media (min-width: 992px) {
            > li {
                display: inline-flex;
                line-height: 1;
                > a {
                    line-height: 85px;
                    padding: 0 20px;
                }
                /*parent item menu*/
                &.current-menu-ancestor,
                &.current-menu-parent,
                &.current_page_parent,
                &.current_page_ancestor {
                    > a {
                    }
                }
                &:not(.menu-item-has-mega-menu) {
                    ul {
                        opacity: 0;
                        visibility: hidden;
                        position: absolute;
                        top: 110%;
                        left: 0;
                        margin-left: 0;
                        background: #fff;
                        min-width: 180px;
                        text-align: left;
                        z-index: 2;
                        @include scaleY(0);
                        ul {
                            top: 110%;
                            @include duration;
                        }
                        li.menu-item-has-children {
                            &:after {
                                content: '\f3d3';
                                font-family: $ion !important;
                                font-size: 12px !important;
                                top: 17px;
                                line-height: 1 !important;
                                position: absolute;
                                right: 10px;
                            }
                        }
                        a {
                            display: block;
                            line-height: 24px;
                            padding: 10px 20px;
                            &:hover {
                                background: rgba(239, 239, 239, 0.28);
                            }
                        }
                    }
                    &:hover {
                        > a {
                        }
                    }
                }
            }
            li {
                position: relative;
                list-style: none;
                &.current-menu-item {
                    a {
                    }
                }
                &:hover {
                    > ul {
                        opacity: 1;
                        visibility: visible;
                        @include scaleY(1);
                        top: 100%;
                    }
                }
                a {
                    &:hover {
                    }
                }
            }
            .sub-menu {
                @include transitions(top 0.3s ease 0s, opacity 0.3s);
                /*improved when resize width*/
                display: $ini !important;
                > li {
                    &:hover {
                        > ul {
                            left: 100%;
                            top: 0;
                        }
                    }
                }
            }
        }
        .sub-menu {
            @media (min-width: 992px) {
                @include shadow(1px 4px 4px 0 rgba(0, 0, 0, 0.1));
                border-radius: 4px;
            }
            a {
                white-space: nowrap;
            }
        }
    }
}
