.ht-pagination,
.woocommerce-pagination {
    @extend .flw;
    ul {
        margin: 0;
        padding-left: 0;
        a {
            &:hover {
                border-color: #999;
            }
        }
        .page-numbers {
            width: 40px;
            height: 40px;
            border: 1px solid #e5e5e5;
            @extend .flex;
        }
        .next,
        .prev {
            min-width: 70px;
        }
        .fa-angle-double-left {
            margin-right: 3px;
        }
        .fa-angle-double-right {
            margin-left: 3px;
        }
        .current {
            background-color: #333;
            color: #fff;
            border-color: $tr;
        }
    }
    li {
        list-style: none;
        display: inline-block;
        + li {
            margin-left: 13px;
        }
    }
}
