#theme-menu-layout {
    @extend .flw;
    position: relative;
}
.menu-layout {
    @extend .flw;
    position: relative;
    z-index: 20;
    @media (min-width: 1380px) {
        .container {
            width: 100%;
            padding-left: 105px;
            padding-right: 105px;
        }
    }
}
.theme-logo {
    font-size: 0;
    margin: 0;
    min-width: 80px;
    max-width: 350px;
    .menu-layout-1 & {
        text-align: center;
    }
}

/*!
------------------------------------------------->*/
.menu-toggle-btn {
	min-width: 26px;
    width: 26px;
    height: 26px;
    background: none;
    border: 0;
    span {
        position: relative;
        background-color: #000;
        width: 100%;
        height: 2px;
        display: block;
        &:before,
        &:after {
            content: '';
            position: absolute;
            left: 0;
            width: 100%;
            height: 2px;
            background-color: $inh;
        }
        &:before {
            top: -6px;
        }
        &:after {
            bottom: -6px;
        }
    }
}

#theme-search-btn {
    padding: 0 5px;
    background: none;
    border: 0;
    font-size: 20px;
    .menu-layout-4 & {
        padding: 0;
    }
}
.menu-layout{
	.header-wishlist-icon{
		font-size: 22px;
	}
}
.header-wishlist-icon{
	margin-left: 15px;
	@media ( max-width: 991px ) {
		margin-right: 15px;
	}
}
.menu-woo-action {
    display: $inh;
    position: relative;
    margin-left: 20px;

    &:hover {
        ul {
            opacity: 1;
            visibility: visible;
            top: 100%;
            @include scaleY(1);
        }
    }
    ul {
        padding-left: 0;
        margin: 0;
        opacity: 0;
        visibility: hidden;
        position: absolute;
        top: 110%;
        left: -50px;
        background: #fff;
        min-width: 180px;
        text-align: left;
        z-index: 2;
        list-style: none;
        border-radius: 4px;
        @include transitions(top 0.3s ease 0s, opacity 0.3s);
        @include shadow(0px 1px 4px 0 rgba(0, 0, 0, 0.2));
        @include scaleY(0);
        a {
            display: block;
            padding: 10px 20px;
            white-space: nowrap;
        }
    }
    li + li {
        border-top: 1px solid #eee;
    }
}
.menu-woo-user,
.menu-woo-cart {
    font-size: 20px;
}
.menu-woo-cart {
    margin-left: 25px;
    margin-right: 15px;
    position: relative;
    .shop-cart-count {
        position: absolute;
        top: -6px;
        left: 80%;
    }
}
.shop-cart-count {
    padding: 0 5px;
    height: 18px;
    color: #fff;
    font-size: 10px;
    font-weight: 700;
    border-radius: 8px;
    min-width: 18px;
    @extend .inline-flex;
}


// Mobile header menu sticky.
.mobile-header-menu-sticky{
	&.mobile-header-fixed{
		&.logged-in.admin-bar{
			#theme-menu-layout{
				.menu-layout{
					@media ( min-width: 783px ) {
						top: 32px;
					}
					@media ( min-width: 601px ) and ( max-width: 782px ) {
						top: 46px;
					}
				}
			}
		}
		#theme-menu-layout{
			.menu-layout{
				position: fixed;
				left: 0;
				top: 0;
				background-color: #fff;
			}
		}
	}
	@media ( max-width: $screen-md - 1 ) {
		&:not(.logged-in):not(.admin-bar){
			#theme-menu-layout{
				.menu-layout{
					position: fixed;
					left: 0;
					top: 0;
					background-color: #fff;
				}
			}
		}
	}
}

// Header layout-5.
@media ( max-width: $screen-md - 1 ) {
	.menu-layout-5 {
		.header-container{
			.m-col{
				flex: 1 1 0px;
			}
			.header-logo{
				text-align: center;
				flex: 0 1 auto;
			}
		}
	}
}