/* elementor widget */.elementor-section{    clear: both;}.elementor-widget{    @extend .flw;}.elementor-widget-container{    min-height: 1px;}.elementor-widget-wp-widget-archives,.elementor-widget-wp-widget-categories{    .elementor-widget-container{        >ul{            padding-left: 0;        }    }    li{        list-style: none;    }    a{        line-height: 35px;    }}/* general */.widget{    float: left;    position: relative;    &:not(.footer-widget){        width: 100%;    }    margin-bottom: 40px;    .widget-title{        margin-bottom: 20px;    }    &:first-of-type{        .widget-title{            margin-top: 0;        }    }    &:not(.footer-widget){        .widget-title{            font-weight: 600;        }    }    >ul{        padding-left: 0;    }    li{        list-style: none;    }    a{        line-height: 35px;    }    select{        @extend .flw;        height: 40px;        padding: 0 15px;    }    .tagcloud{        a{            line-height: unset;        }    }}.widget_archive,.widget_categories,.elementor-widget-wp-widget-archives,.elementor-widget-wp-widget-categories,.widget_pages,.widget_meta,.widget_recent_entries,.widget_nav_menu{    &:not(.footer-widget){        li{            border-bottom: 1px dotted #e3e3e3;            >ul{                border-top: 1px dotted #e3e3e3;                li{                    &:last-child{                        border-bottom: 0;                    }                }            }        }        a{            margin-right: 10px;            padding: 3px 0;            display: inline-block        }    }}/* search */.search-form{    @extend .flw;    position: relative;    .search-no-results &{        max-width: 400px;    }}.search-field{    @extend .flw;    height: 40px;    border-radius: 20px;    border: 1px solid #eeeeee;    background: #f7f7f7;    padding: 0 40px 0 20px;    &:focus{        border-color: #ddd;    }}.search-submit{    position: absolute;    border-radius: 50%;    width: 40px;    height: 40px;    border: 0;    right: 0;    top: 0;    background: $tr;    font-size: 16px;    @extend .flex;}/* menu */.widget_nav_menu{    >div{        >ul{            padding-left: 0;        }    }    li{        position: relative;        ul{            margin-left: 15px;        }    }    .theme-menu-label{        float: right;        cursor: default;        pointer-events: none;    }}/* flickr */.flickr-widget{    @extend .flw;    @include flex(flex, space-between, flex-start);    flex-wrap: wrap;    &.flickr-col-1{        a{            width: 100%;        }    }    &.flickr-col-2{        a{            @include calc( 'flex-basis', '(100% - 10px) / 2' );        }    }    &.flickr-col-3{        a{            @include calc( 'flex-basis', '(100% - 20px) / 3' );        }    }    &.flickr-col-4{        a{            @include calc( 'flex-basis', '(100% - 30px) / 4' );        }    }    &.flickr-col-5{        a{            @include calc( 'flex-basis', '(100% - 40px) / 5' );        }    }    a{        margin-bottom: 10px;    }}