.menu-layout--centered {
    .menu-toggle {
        height: 26px;
    }
	.header-container {
		display: flex;
		align-items: center;
		padding: 15px 0;
	}

	.header-content {
		margin-right: auto;
		margin-left: auto;
	}

	.theme-logo {
		display: flex;
		justify-content: center;
		align-items: center;
	}

	.header-menu {
		display: none;
	}

	#theme-search-btn {
		display: none;
	}

	@media ( min-width: #{ $screen-md } ) {
		.header-container {
			justify-content: space-between;
			padding: 0;
		}

		.header-content {
			display: flex;
			align-items: center;
		}

		.header-logo {
			padding: 0 20px 0;
		}

		.header-menu {
			display: block;
		}

		.header-action {
			display: flex;
			flex-basis: 80px;
		}

		.menu-toggle {
			flex-basis: 80px;
		}

		.menu-toggle-btn {
			display: none;
		}

		#theme-search-btn {
			display: inline-block;
		}
	}
}

.menu-layout-4 {
	#theme-search-btn,
	.menu-woo-user,
	.menu-woo-cart {
        @media ( min-width: #{ $screen-md } ) {
            color: #ffffff $i;
        }
	}
}
