@media ( min-width: #{ $screen-md } ) {
    .menu-layout-4 {
        .primary-logo {
            display: none;
        }
    }
}

@media ( max-width: #{ $screen-md - 1 } ) {
    .menu-layout {
        &:not(.menu-layout-5) {
            .menu-woo-action,
            #theme-search-btn {
                display: none;
            }
            .menu-woo-cart {
                margin-left: 0;
            }
        }
        &.menu-layout-5 {
            .menu-toggle-btn {
                margin-right: 0;
            }
            .header-logo {
                text-align: center;
            }
            &.menu-layout--vertical {
                .logo {
                    /*iconbar width*/
                    margin-left: -26px;
                }
            }
        }
    }

    .menu-toggle-btn {
        margin-right: 9px;
    }

    .menu-layout--transparent {
        position: static;
    }

    .menu-layout--classic {
        .header-logo,
        .header-container__inner {
            text-align: center;
        }
    }

    .menu-layout-4 {
        .secondary-logo {
            display: none;
        }
        .menu-toggle {
            flex-basis: 35px;
        }
    }
}

.menu-layout-1 {
    #menu-box {
        padding: 15px 0;
    }
}
