.dropdown {
	position: relative;

	&:not(:last-child) {
		margin-right: 25px;
	}

	&:hover {
		.dropdown__content {
			display: block;
			z-index: 9999;
		}
	}
}

.dropdown__content {
	position: absolute;
	display: none;
	min-width: 80px;
	background-color: #f1f1f3;
	box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.2);
	a {
		display: block;
		padding: 5px 10px;
	}
}
