.quick-viewing{    #shop-quick-view{        &:before{            @include loading( $size: 30px, $color: #222, $base_color: #ddd, $border_size: 2px )        }    }}.quick-view-open{    overflow: hidden;    #shop-quick-view{        opacity: 1;        visibility: visible;        pointer-events: auto;        @include translatey( 0 );    }    #shop-overlay{        opacity: 1;        visibility: visible;        pointer-events: auto;    }}#shop-quick-view{    position: fixed;    opacity: 0;    visibility: hidden;    pointer-events: none;    height: 600px;    width: 1200px;    overflow: hidden;    background: #fff;    z-index: 200;    @include calc( top, '( 100% - 600px ) / 2' );    @include calc( left, '( 100% - 1200px ) / 2' );    @include translatey( 50px );    @extend .duration;}#quick-view-gallery{    height: 600px;    &:not(.quick-view-slider){        max-width: 100%;    }}.quick-view-content{    position: relative;    .product-quick-view-btn{        display: none;    }    .product-type-grouped{        .group_table{            .yith-wcwl-add-to-wishlist{                margin-left: 0;            }        }    }    .quick-view-images{        width: 50%;        float: left;        .tns-controls{            button{                opacity: 0;                @extend .duration;                &[data-controls="prev"]{                    left: 0;                }                &[data-controls="next"]{                    right: 0;                }            }        }        .tns-nav{            @extend .duration;            opacity: 0;            bottom: 20px;        }        &:hover{            .tns-nav{                opacity: 1;                bottom: 30px;            }            .tns-controls{                button{                    opacity: 1;                    &[data-controls="prev"]{                        left: 20px;                    }                    &[data-controls="next"]{                        right: 20px;                    }                }            }        }    }    .summary{        width: 50%;        float: left;        height: 600px;        padding: 40px 50px;        overflow: auto;    }    .product_title{        margin-top: 0;        font-weight: normal;        font-size: 28px;    }    .woocommerce-product-details__short-description{        margin-top: 10px;        margin-bottom: 30px;    }    .quantity{        height: 40px;        width: 110px;        .qty{            font-size: 14px;        }        &:not(.hidden){            .modify-qty{                font-size: 20px;                justify-content: center;                padding: 0;                width: 40px;                z-index: 10;            }        }    }    .single_add_to_cart_button{        min-width: 150px;        width: $ini;        padding: 0 15px;        height: 40px;        font-size: 12px;        float: left;        &:before{            margin-right: 7px;        }    }    .yith-wcwl-add-to-wishlist{        clear: none;        margin-left: 10px;        margin-top: -5px;        a{            font-size: 14px;        }    }    .product_meta{        padding-top: 15px;        margin-top: 30px;        span{            font-size: 13px;        }    }    .p-shared{        margin-top: 0;        >span{            display: inline-block;            margin-right: 10px;            font-weight: 700;        }    }    @media ( max-width: 600px ) {		.cart {			.yith-wcwl-add-to-wishlist{				clear: both;				margin-left: 0;				margin-top: 15px;			}		}	}	@media ( max-width: 400px ) {		.cart {			.quantity{				width: 100%;			}			.button{				margin-left: 0;				width: 100%;				margin-top: 10px;			}		}	}}.quick-view-close-btn{    position: absolute;    right: 0;    top: 0;    width: 40px;    height: 40px;    cursor: pointer;    font-size: 30px;    z-index: 2;    background: none;    border: 0;    @extend .flex;    @extend .duration;    &:hover{        @include rotate( 90 );    }}.quick-view-images{    position: relative;}#shop-quick-view {    .size-guide-button,    .size-guide {        display: none;    }}