#main {
	clear: both;
	&[class*="col-"] {
		clear: none;
	}
}
.not-found {
	clear: both;
	&:after {
		content: " ";
		display: table;
	}
	&:after {
		clear: both;
	}	
	padding: 100px 0 30px;
	h2.title {
		font-size: 80px;
		@media (min-width: 991px) {
			margin-top: 150px;
		}
	}
	h3.sub-title {
		font-size: 30px;
		padding-top: 10px;
	}
	figure {
		@media (min-width: 991px) {
			margin-bottom: 155px;
		}
	}
	.back-to-home {
		color: $ini;
		font-size: 15px;
		line-height: 18px;
		border-bottom: 2px solid #dd2a2a;
		display: inline-block;
		margin: 45px 0;
	}
	p.subscript {
		margin-left: 40px;
		padding-top: 4px;
	}
	.get-in-touch {
		font-size: 42px;
		margin: 0;
	}
	.subscription-widget {
		margin-top: 15px;
		input,
		button {
			font-size: 14px;
		}
	}
}

[data-elementor-type] {
	clear: both;
}