.updating-cart {    &.cart-sidebar-open {        #shop-cart-sidebar {            &:after {                @include duration(0s);            }        }    }    &.ajax-single-add-to-cart {        .single_add_to_cart_button.loading {            &:before {                @include loading();                height: 15px;                width: 15px;                position: static;                display: inline-flex;                margin-top: 0;                margin-left: 0;            }        }        &.quick-view-open {            .single_add_to_cart_button {                &:before {                    height: 12px;                    width: 12px;                }            }        }    }    #shop-cart-sidebar {        @extend .circle-loading;        &:before {            @include calc(top, '50% + 30px');            z-index: 3;        }        &:after {            opacity: 1;            visibility: visible;        }    }}.cart-sidebar-open {    &.cart-updated {        #shop-cart-sidebar {            &:before {                content: none $i;            }            &:after {                opacity: 0;                visibility: hidden;                pointer-events: none;            }        }    }    #shop-cart-sidebar {        visibility: visible;        pointer-events: auto;        @include translate3d(0, 0, 0);    }    #shop-overlay {        opacity: 1;        visibility: visible;        pointer-events: auto;    }}#shop-cart-sidebar {    position: fixed;    top: 0;    right: 0;    bottom: 0;    z-index: 200;    width: 300px;    background: #ffffff;    visibility: hidden;    pointer-events: none;    @include translate3d(100%, 0, 0);    @include transition(all 0.3s);    .logged-in.admin-bar & {        top: 46px;        @media (min-width: 783px) {            top: 32px;        }    }    &:after {        content: '';        position: absolute;        left: 0;        top: 0;        right: 0;        bottom: 0;        background: $inh;        z-index: 2;        opacity: 0;        visibility: hidden;        @extend .duration;    }}.cart-sidebar-head {    border-bottom: 1px solid #ddd;    padding: 20px;    position: relative;    z-index: 3;    min-height: 60px;    background: $inh;    @include flex(flex, flex-start, center);    .shop-cart-count {        margin-left: 10px;    }}.cart-sidebar-title {    margin: 0;    display: inline-block;    text-transform: uppercase;    font-size: 13px;    white-space: nowrap;}#close-cart-sidebar {    border: 0;    background: none;    position: absolute;    right: 20px;    top: 50%;    font-size: 20px;    width: 30px;    height: 30px;    @extend .duration;    @extend .flex;    @include transforms(translateY(-50%) rotate(0deg));    &:hover {        @include transforms(translateY(-50%) rotate(90deg));    }}.cart-sidebar-content {    overflow: auto;    @include calc(height, '100% - 140px');    .woocommerce-mini-cart__total {        position: absolute;        margin-bottom: 0;        bottom: 40px;        background: #fff;        width: 100%;        height: 40px;        padding: 0 20px;        text-transform: uppercase;        @include flex(flex, space-between, center);        .amount {            font-weight: 700;        }    }    .woocommerce-mini-cart__buttons {        margin-bottom: 0;        position: absolute;        bottom: 0;        width: 100%;        a {            width: 50%;            height: 40px;            background: #f3f3f3;            text-transform: uppercase;            @extend .inline-flex;        }        .checkout {            color: #fff;        }    }    .woocommerce-mini-cart__empty-message {        margin-bottom: 0;        padding: 20px;    }    .variation {        margin: 5px 0;        font-size: 13px;        @extend .flw;        dt,        dd {            float: left;            line-height: 18px;        }        dt {            font-weight: normal;            clear: both;            margin-right: 5px;        }        dd {            margin: 0;            p:last-child {                margin-bottom: 0;            }        }    }    .quantity {        height: auto;        @extend .flw;        font-size: 13px;    }}/* CART ITEM */.product_list_widget {    @extend .flw;    margin: 0;    padding-left: 0;    list-style: none;    .blockUI.blockOverlay {        background: #f3f3f3 $i;        opacity: 0.7 $i;        cursor: pointer $i;    }    .mini_cart_item {        @extend .flw;        min-height: 110px;        position: relative;        padding: 20px 20px 20px 110px;        border-bottom: 1px solid #eaeaec;        &:hover {            .remove_from_cart_button {                opacity: 1;                @include transforms(translateY(-50%) translateX(0) rotate(0deg));            }        }    }    img {        width: 70px;        position: absolute;        left: 20px;        top: 20px;    }    a:not(.remove_from_cart_button) {        display: block;    }    .remove_from_cart_button {        width: 20px;        height: 20px;        position: absolute;        right: 20px;        top: 50%;        font-size: 0;        color: $inh;        opacity: 0;        z-index: 2;        @extend .flex;        @include transforms(translateY(-50%) translateX(10px) rotate(0deg));        &:before {            content: '\f2d7';            font-family: $ion;            line-height: 1;            font-size: 14px;        }        &:focus {            @extend .circle-loading;        }        &:hover {            @include transforms(translateY(-50%) translateX(0) rotate(90deg) $i);        }    }}#shop-overlay {    position: fixed;    left: 0;    bottom: 0;    right: 0;    top: 0;    z-index: 199;    background: rgba(0, 0, 0, 0.5);    pointer-events: none;    opacity: 0;    visibility: hidden;    @extend .duration;}