.single {
  .blog-article {
    margin-bottom: 65px;
  }
  .cover-image {
    margin-bottom: 20px;
  }
  .blog-article-header {
    display: flex;
    display: -webkit-flex;
    flex-direction: column;
    .blog-header-info {
      order: 2;
      display: flex;
      display: -webkit-flex;
      margin-bottom: 40px;
      .if-date {
        order: 1;
      }
    }
    .entry-header {
      order: 1;
    }
  }
  .blog-header-info {
    .if-item {
      text-transform: none;
      order: 2;
      a {
        text-transform: uppercase;
        font-weight: 600;
      }
    }
  }
}

.blog-article {
  .entry-footer {
    .tagcloud {
      @extend .flw;
      .single & {
        border-top: 1px solid #eeeeee;
        padding: 30px 0;
        margin-top: 40px;
      }
    }
  }
  .theme-social-icon {
    float: right;
    a {
      color: #999999;
      width: 30px;
      height: 30px;
      @extend .inline-flex;
      &:before {
        font-size: 20px;
      }
    }
  }
}

.tagcloud {
  a {
    height: 30px;
    padding: 0 15px;
    border-radius: 50px;
    display: inline-block;
    border: 1px solid #dddddd;
    margin-right: 8px;
    margin-bottom: 8px;
  }
}

.posts-nav {
  margin-top: 40px;
  background: #f4f4f4;
  @extend .flw;
  @include flex(flex, space-between, center);
}

.post-nav-item {
  padding: 60px;
  width: 50%;
  @extend .flex;
  &.prev-nav {
    justify-content: flex-start;
    img {
      margin-right: 30px;
    }
  }
  &.next-nav {
    justify-content: flex-end;
    align-self: stretch;
    background: #ededed;
    text-align: right;
    .nav-item-cont {
      margin-right: 30px;
    }
  }
  .nav-item-cont {
    display: -webkit-inline-flex;
    display: inline-flex;
    flex-direction: column;
    min-height: 100px;
    justify-content: center;
    @include calc(max-width, '100% - 130px');
    .entry-title {
      margin: 0;
      font-size: 20px;
      font-weight: normal;
      line-height: 30px;
    }
    a:hover {
      text-decoration: underline;
    }
    span {
      text-transform: uppercase;
      font-size: 10px;
    }
  }
  img {
    width: 100px;
    height: 100px;
    object-fit: cover;
  }
}

.post-layout-classic {
  .entry-thumbnail {
    margin-bottom: 20px;
  }
}

.entry-meta {
  display: inline-block;
  margin-bottom: 10px;
  font-size: 12px;
}

.entry-categories {
  display: inline-block;

  &:after {
    content: '/';
    display: inline-block;
    padding: 0 5px;
  }

  a {
    display: inline-block;
    color: #222222;
    border-bottom: 2px solid;
    line-height: 1;
  }
}

.entry-title {
  margin-top: 0;
  margin-bottom: 20px;
  font-size: 30px;
  font-weight: 400;
}

.entry-wrapper--overlap {
  position: relative;
  top: -70px;
  margin: 0 20px -70px;
  padding: 15px 20px;
  background-color: #fff;

  @media (min-width: 992px) {
    padding: 30px 40px;
    margin: 0 40px -70px;
  }

  .entry-categories a {
    border-bottom: 0;
  }

  .entry-title {
    margin-top: 5px;
    font-weight: 600;
  }
}
