.menu-layout {
	&.menu-layout-5 {
		border-bottom: 0;
	}
}

.sidebar-menu-middle {
	flex-grow: 1;
}

.menu-layout--vertical {
	.header-container {
		display: flex;
		align-items: center;
		padding: 15px 0;
	}

	.header-logo {
		margin-right: auto;
		margin-left: auto;
	}

	@media ( min-width: #{ $screen-md } ) {
		.header-container {
			display: none;
		}
	}
}

#sidebar-menu-content {
	display: flex;
	flex-direction: column;
	justify-content: space-between;
	overflow-y: auto;
}

#sidebar-menu-content.sidebar-menu--md-visible {

	@media ( min-width: #{ $screen-md } ) {
		@include translate3d(0, 0, 0);
		visibility: visible;
		z-index: 100;
		width: 275px;
		padding: 30px;
	}

	@media (min-width: 1400px) {
		width: 345px;
		padding: 40px 60px;
	}

	.header-action {
		display: flex;
		align-items: center;
	}
}
.content-container {
	@media ( min-width: #{ $screen-md } ) {
		position: relative;
		@include translatex(275px);
		@include calc(width, '100% - 275px');
		.container {
			width: 100%;
		}
		#theme-menu-layout {
			display: none;
		}
	}

	@media (min-width: 1400px) {
		@include translatex(345px);
		@include calc(width, '100% - 345px');
	}
}

.sidebar-menu-top {
	margin-bottom: 20px;
}

.sidebar-menu-bottom {
	margin-top: 20px;
}

.sidebar-menu-links,
.sidebar-menu-social {
	margin: 0;
	padding: 0;
	list-style: none;
}

.sidebar-menu-links {
	position: relative;
	margin-bottom: 25px;
	padding-bottom: 30px;

	&:after {
		content: '';
		position: absolute;
		bottom: 0;
		display: inline-block;
		width: 60px;
		height: 1px;
		background-color: #dddddd;
	}

	li:not(:last-child) {
		margin-bottom: 5px;
	}
}

.sidebar-menu-social {
	margin-bottom: 30px;
}

.menu-social {
	margin: 0;
	padding: 0;
	list-style: none;

	li {
		display: inline-block;

		&:not(:last-child) {
			margin-right: 15px;
		}
	}

	a:before {
		font-size: 16px;
		font-family: 'ionicons';
	}

	a[href*='facebook.com']:before {
		content: '\f231';
	}

	a[href*='twitter.com']:before {
		content: '\f243';
	}

	a[href*='instagram.com']:before {
		content: '\f350';
	}
}

.sidebar-actions {
	margin: 0;
	padding: 0;
	list-style: none;
}

.sidebar-action {
	padding-top: 15px;
	padding-bottom: 15px;
	border-top: 1px solid #eeeeee;
	font-size: 18px;

	&:last-child {
		padding-bottom: 0;
	}
}

.sidebar-action-link {
	position: relative;
	display: block;
}

.sidebar-action-icon {
	display: inline-block;
	margin-right: 10px;
}

.sidebar-action-cart {
	position: absolute;
	right: 0;
	top: 50%;
	display: inline-block;
	height: 18px;
	padding: 0 5px;
	color: #ffffff;
	border-radius: 8px;
	line-height: 18px;
	font-size: 10px;
	font-weight: 700;
	text-align: center;
	transform: translateY(-60%);
}
